<template>
  <div class="profile-info">
    <div v-if="!loading" class="tab" :style="computedStyle">
      <div class="row">
        <div class="flex xs12 xl12">
          <div
            v-for="info in Object.keys(profile)"
            :key="info"
            class="tab__item d-flex align--center"
          >
            <va-icon :name="infoIcon(info)" class="ml-3 mr-3"></va-icon>
            <div class="text--bold tab__item-name mr-5">
              {{ $t('dashboard.profile.tabs.info.' + info) }}
            </div>
            <div v-if="typeof profile[info] != 'object'">
              <va-select
                :options="emailsToBind"
                v-model="selectedManager"
                @input="managerSelected"
                @click="update - users"
                :label="$t('dashboard.profile.tabs.info.manager')"
                no-clear
                width="200px"
                v-if="canBeBinded && info == 'binded-to'"
              />
              <span v-else>{{ profile[info] }}</span>
            </div>
            <div v-else>
              <span v-for="(el, i) in profile[info]" :key="i">
                <a :href="el.url" target="_blank">
                  {{ el.amount + ' ' + el.net }}
                </a>
                {{ i < profile[info].length - 1 ? ' | ' : '' }}
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <va-card-no-data
      v-else
      :loading="loading"
      :loading-text="$t('dashboard.no-data.profile-info.loading')"
      :empty-text="$t('dashboard.no-data.profile-info.empty')"
    />
  </div>
</template>

<script>
import { bindToManager, abort } from '@/api/Users'

import { mapGetters } from 'vuex'
import VaCardNoData from '../components/VaCardNoData.vue'

export default {
  name: 'profile-info-tab',
  components: {
    VaCardNoData,
  },
  props: {
    loading: Boolean,
    user: Object,
    me: Object,
  },
  data: () => ({
    selectedManager: 'Nobody',
  }),
  watch: {
    'user.bindedTo': 'updateManagerUserBindedTo',
  },
  computed: {
    ...mapGetters({
      managers: 'managers',
    }),
    computedStyle() {
      return {
        color: this.$themes.brightForeground,
        opacity: this.loading ? 0.2 : 1,
      }
    },
    myRole() {
      return this.me ? this.me.role : 'owner'
    },
    canBeBinded() {
      return this.myRole == 'owner' && this.user.role == 'user'
    },
    profile() {
      let p = {
        role: this.user.role.toUpperCase(),
        email: this.user.email,
        registered: this.$time.format(
          this.$time.UTCToLocale(this.user.registered)
        ),
      }

      if (!this.user.me && this.user.role == 'user')
        p['binded-to'] = this.user.bindedTo || 'nobody'

      if (
        this.user.transfers != '...' &&
        this.user.transfers &&
        this.user.transfers.length
      )
        p.transfers = this.user.transfers
      if (!this.user.me && this.user.location)
        p.location = `${this.user.location.country}, ${this.user.location.city} (${this.user.location.ip})`

      return p
    },
    emailsToBind() {
      return ['Nobody', 'Me', ...this.managers.map(m => m.email)]
    },
  },
  methods: {
    managerSelected(email) {
      let self = this
      let userId = this.user.id
      let managerId = ''
      let managerEmail = ''
      let manager = null

      if (email == 'Nobody') {
        return
      } else if (email == 'Me') {
        managerId = this.me.id
        managerEmail = this.me.email
      } else {
        this.managers.forEach(m => {
          if (m.email == email) {
            manager = m
            managerId = m.id
            managerEmail = m.email
          }
        })
      }

      if (managerId) {
        this.loading = true

        bindToManager(userId, managerId)
          .then(({ data }) => {
            self.loading = false

            self.showToast(data.message, {
              icon: 'fa-check',
              position: 'bottom-center',
              duration: 4000,
              type: 'success',
            })
          })
          .catch(({ response }) => {
            let error = response ? response.data.message : 'Connection error'

            self.loading = false

            self.showToast(error, {
              icon: 'fa-times',
              position: 'bottom-center',
              duration: 4000,
              type: 'primary',
            })
          })
      }
    },
    infoIcon(name) {
      return {
        ID: 'fa fa-id-card-o',
        Role: 'fa fa-user-o',
        Username: 'fa fa-address-book',
        'E-mail': 'fa fa-envelope-o',
        Transfers: 'fa fa-money',
        'Binded to': 'fa fa-link',
        Location: 'fa fa-map-marker',
      }[name]
    },
    updateManagerUserBindedTo(email) {
      if (this.me && this.me.email == email) this.selectedManager = 'Me'
      else if (email) this.selectedManager = email
      else this.selectedManager = 'Nobody'
    },
  },
  mounted() {
    if (this.user) this.updateManagerUserBindedTo(this.user.bindedTo)
  },
}
</script>

<style lang="scss" scoped>
@import '../../../sass/smart-grid';

.no-data {
  min-height: 65px;
  padding-top: 0.5rem;
  font-size: 1.2rem;
  text-align: center;
}
.tab {
  width: 100%;

  a {
    color: #55a1fb;
  }

  a:hover {
    text-decoration: underline;
  }

  &__item {
    min-height: 55px;
    width: calc(100% + 40px);
    margin: 0 -20px;
    padding: 15px 20px;
    align-items: flex-start;

    @include to($break_sm) {
      padding-left: 40px;
      flex-direction: column;
    }
    @include from($break_sm) {
      flex-direction: row;
      padding-left: 20px;
    }

    &-name {
      min-width: 60px;
      max-width: 150px;

      @include to($break_sm) {
        margin-bottom: 5px;
      }
      @include from($break_sm) {
        margin: 0;
      }
    }

    &:nth-child(odd) {
      background-color: #00000009;
    }
  }
}
</style>
