const state = {
  sidebar: {
    opened: false,
  },
  config: {
    base: window.location.protocol + '//' + (window.location.host.startsWith('localhost') ? 'localhost:8080' : window.location.host),
    palette: {
      primary: '#4ae387',
      danger: '#e34a4a',
      info: '#4ab2e3',
      success: '#db76df',
      warning: '#f7cc36',
      white: '#fff',
      black: '#000',
      fontColor: '#34495e',
      transparent: 'transparent',
      lighterGray: '#ddd',
    },
  },
  isLoading: true,
  isDesktop: true
}

const mutations = {
  setLoading(state, isLoading) {
    state.isLoading = isLoading
  },
  IT_IS_DESKTOP(state, yes) {
    state.isDesktop = yes
  },
}

const getters = {
  BASE: state => state.config.base
}

export default {
  state,
  mutations,
  getters,
}
