import Vue from 'vue'
import Router from 'vue-router'
import axios from 'axios'

import store from '../store'
import AppLayout from '../components/admin/AppLayout'
import Dashboard from '../components/dashboard/Dashboard'

Vue.use(Router)

const EmptyParentComponent = {
  template: '<router-view></router-view>',
}

var demoAuthCredentials = {
  email: 'design.lmcorp@gmail.com',
  password: 'useruserhelp',
}

demoAuthCredentials = null

const routes = [
  {
    path: '*',
    redirect: { name: 'dashboard' },
  },
  {
    path: '/404',
    component: () =>
      import('@/components/pages/404-pages/VaPageNotFoundSimple.vue'),
  },
  {
    name: 'Admin',
    path: '/admin',
    redirect: '/admin/dashboard',
    component: AppLayout,
    children: [
      {
        name: 'dashboard',
        path: 'dashboard',
        component: Dashboard,
        default: true,
      },
      {
        name: 'deposits',
        path: 'deposits',
        component: () => import('../components/deposits/Deposits.vue'),
      },
      {
        name: 'settings',
        path: 'settings',
        component: () => import('../components/settings/Settings.vue'),
      },
      {
        name: 'domains',
        path: 'domains',
        component: () => import('../components/domains/Domains.vue'),
      },
    ],
  },
]

const router = new Router({
  mode: 'history',
  routes,
})

router.beforeResolve((to, from, next) => {
  if (demoAuthCredentials && !localStorage.getItem('auth-token')) {
    axios
      .post(store.getters.BASE + '/api/auth/signin', demoAuthCredentials)
      .then(({ data }) => {
        axios
          .get(store.getters.BASE + '/api/admin/auth', {
            headers: {
              Authorization: data.token,
            },
          })
          .then(({ data: user }) => {
            store.commit('AUTH', { user, token: data.token })
            window.location.reload(false)
            localStorage.setItem('auth-token', data.token)
            next()
          })
          .catch(err => {
            sessionStorage.removeItem('auth-token')
          })
      })
  } else if (!store.state.user) {
    axios
      .get(store.getters.BASE + '/api/admin/auth', {
        headers: {
          Authorization: localStorage.getItem('auth-token'),
        },
      })
      .then(response => {
        store.commit('AUTH', { user: response.data, token: localStorage.getItem('auth-token') })
        next()
      })
      .catch(err => {
        sessionStorage.removeItem('auth-token')
      })
  } else {
    next()
  }
})

export default router
