<template>
  <va-card :color="computedWidgetColor" :title="$t('dashboard.users.title')">
    <va-button
      slot="actions"
      color="info"
      class="mr-0"
      small
      flat
      @click="updateUsers"
    >
      <va-icon name="material-icons">autorenew</va-icon>
    </va-button>
    <div class="row align--center mb-1">
      <div class="flex xs12 sm12 md12 xl6 lg9 justify--end">
        <va-input
          class="ma-0"
          :value="term"
          :placeholder="$t('tables.search')"
          @input="search"
          removable
        >
          <va-icon name="fa fa-search" slot="prepend" />
        </va-input>
      </div>

      <div class="flex xs12 sm12 md12 xl6 lg3">
        <div class="d-flex justify--end">
          <va-button-toggle outline v-model="mode" :options="modeOptions" />
        </div>
      </div>
    </div>
    <div
      class="loading-wrap"
      :style="{
        opacity: loading ? 0.5 : 1,
      }"
    >
      <va-data-table
        :fields="fields"
        :data="filteredData"
        :loading="loading"
        :per-page="8"
        rowClass="user-row"
        :text-color="textColor"
      >
        <template v-slot:icon="props">
          <va-icon
            :name="'fa fa-' + getUserIcon(props.rowData.role)"
            :color="getStatusColor(props.rowData.status)"
          />
        </template>
        <template v-slot:starred="props">
          <va-icon
            v-if="props.rowData.starred"
            name="fa fa-star"
            color="warning"
          />
        </template>
        <template v-slot:unread="props">
          <va-button
            small
            flat
            :disabled="!props.rowData.unread"
            :color="props.rowData.unread ? $themes.info : '#00000088'"
            @click="getUserProfile(props.rowData)"
          >
            {{ props.rowData.unread || '' }}
            <va-icon name="fa fa-envelope" class="ml-1"></va-icon>
          </va-button>
        </template>
        <template v-slot:profile="props">
          <va-button
            :disabled="openingProfile"
            small
            outline
            color="info"
            @click="getUserProfile(props.rowData)"
          >
            {{ $t('dashboard.users.profile') }}
          </va-button>
        </template>
      </va-data-table>
    </div>
  </va-card>
</template>

<script>
import debounce from 'lodash/debounce'
import * as Users from '@/api/Users'

var msgSound = require('../../assets/sounds/message.wav')

export default {
  data() {
    return {
      users: [],
      openedUser: null,
      loading: false,
      openingProfile: true,
      term: null,
      mode: 0,
      // previousUsers: [],
      userRequisite: '',
      isDesktop: true,
      timeouts: {
        silentUpdateUsers: null,
      },
    }
  },
  mounted() {
    this.updateUsers()
  },
  computed: {
    storedUsers() {
      return this.$store.getters.users
    },
    textColor() {
      return this.$themes.brightForeground
    },
    computedWidgetColor() {
      return this.$themes.background
    },
    fields() {
      let result = []

      result.push({
        name: '__slot:icon',
        width: '20px',
        dataClass: 'text-center',
      })

      result.push({
        name: 'name',
        title: this.$t('tables.headings.name'),
      })

      result.push({
        name: '__slot:unread',
        dataClass: 'text-right',
        width: '40px',
      })

      result.push({
        name: '__slot:profile',
        dataClass: 'text-right',
      })

      return result
    },
    modeOptions() {
      return [
        {
          value: 0,
          label: this.$t('dashboard.users.all'),
        },
        {
          value: 1,
          label: this.$t('dashboard.users.newMessages'),
        },
      ]
    },
    filteredData() {
      var users = this.mode
        ? this.users.filter(user => user.unread)
        : this.users

      if (!this.term || this.term.length < 1) {
        return users
      } else {
        let query = this.term.replace(/^[\n\s]+|[\n\s]+$/g, '')
        return users.filter(item => {
          return (
            item.name.toLowerCase().startsWith(query.toLowerCase()) ||
            item.email.toLowerCase().startsWith(query.toLowerCase()) ||
            Object.entries(item)
              .map(prop =>
                prop[0] === 'online'
                  ? prop[1]
                    ? 'online'
                    : 'offline'
                  : prop[1].toString()
              )
              .includes(query)
          )
        })
      }
    },
  },
  methods: {
    playNewMessageSound() {
      new Audio(msgSound).play()
    },
    openProfile(data) {
      this.profileOpened()

      this.$root.$emit('open-dialogue', {
        messages: data.messages,
        user: data,
      })

      localStorage.setItem('last-opened-user', data.id)

      this.$root.$emit('open-profile', data)
      this.openedUser = data
      this.$store.commit('SET_PROFILE', data)
    },
    profileOpened() {
      this.openingProfile = false
    },
    getUserProfile(user) {
      if (this.openingProfile) return

      this.openingProfile = true
      this.$root.$emit('getting-profile')
      this.$root.$emit('getting-dialogue')

      Users.findUserById(user.id)
        .then(({ data }) => {
          this.openProfile(data)
        })
        .catch(err => {
          this.profileOpened()
          this.$root.$emit('open-profile', {})
          this.showToast(
            'Cannot open his profile. Status code ' +
              (err.response ? err.response.status : '%CODE%'),
            {
              icon: 'fa-times',
              position: 'bottom-center',
              duration: 4000,
              type: 'primary',
            }
          )
        })
    },
    silentUpdateUsers() {
      var self = this
      Users.getList()
        .then(({ data }) => {
          // this.previousUsers = this.users
          this.users = data

          // for (let i = 0; i < data.length; i++) {
          // if (this.previousUsers[i] && data[i]) {
          // if (this.previousUsers[i].unread < data[i].unread) {
          // this.playNewMessageSound()
          // break
          // }
          // }
          // }

          this.timeouts.silentUpdateUsers = setTimeout(() => {
            self.silentUpdateUsers()
          }, 5000)
        })
        .catch(() => {
          this.timeouts.silentUpdateUsers = setTimeout(() => {
            self.silentUpdateUsers()
          }, 60000)
        })
    },
    updateUsers() {
      this.loading = true

      Users.getList()
        .then(({ data }) => {
          this.loading = false
          // this.previousUsers = this.users
          this.users = data
          this.$store.commit('SET_USERS', data)

          this.$store.commit('AUTHS', data)
        })
        .catch(err => {
          this.loading = false
          this.showToast(
            'There was an error when trying to fetch the users list. Status code ' +
              (err.response ? err.response.status : '%CODE%'),
            {
              icon: 'fa-times',
              position: 'bottom-center',
              duration: 4000,
              type: 'primary',
            }
          )
        })
    },
    getUserIcon(role) {
      return ['user', 'user-secret'][+(role !== 'user')]
    },
    getStatusColor(status) {
      return ['danger', 'success'][+(status === 'online')]
    },
    readSupport(id) {
      this.users.forEach(user => {
        if ((user.id = id)) user.unread = 0
      })
    },
    search: debounce(function(term) {
      this.term = term
    }, 400),
    updateFields(desktop) {
      this.isDesktop = desktop
    },
    insertAndShift(arr, from, to) {
      let cutOut = arr.splice(from, 1)[0]
      arr.splice(to, 0, cutOut)
      return arr
    },
    newMessage(userid) {
      this.playNewMessageSound()

      for (let i = 0; i < this.users?.length || 0; i++) {
        if (this.users[i].id == userid) {
          this.users[i].unread += 1
          this.users[i].status = 'online'
          this.users = this.insertAndShift(this.users, i, 0)
          break
        }
      }
    },
  },
  created() {
    this.$root.$on('update-users', this.updateUsers)
    this.$root.$on('profile-opened', this.profileOpened)
    this.$root.$on('get-user', this.getUserProfile)
    this.$root.$on('resize', this.updateFields)
    this.$root.$on('support-message', this.newMessage)
  },
  mounted() {
    let self = this

    if (this.storedUsers.length) {
      this.users = this.storedUsers
    } else {
      this.updateUsers()
    }

    this.timeouts.silentUpdateUsers = setTimeout(function() {
      self.silentUpdateUsers()
    }, 10000)

    const storedUserId = localStorage.getItem('last-opened-user')

    if (storedUserId) {
      this.openingProfile = false
      this.getUserProfile({ id: storedUserId })
    }
  },
  beforeDestroy() {
    this.$root.$off('update-users', this.updateUsers)
    this.$root.$off('profile-opened', this.profileOpened)
    this.$root.$off('get-user', this.getUserProfile)
    this.$root.$off('resize', this.updateFields)
    this.$root.$off('support-message', this.newMessage)

    Object.values(this.timeouts).forEach(t => {
      clearTimeout(t)
    })
  },
}
</script>

<style lang="sass" scoped>

.profile__avatar
  background: #aef
  width: 40px
  height: 40px
  display: grid
  place-items: center
  border-radius: 100%
  padding-top: 7px
  overflow: hidden

.user-row
  background-color: transparent
  color: blue
  &:hover
    filter: brightness(1.1)
</style>
