<template>
  <va-card
    :color="$themes.background"
    :style="computedWidgetStyle"
    :title="computedTitle"
  >
    <div class="tab">
      <va-tabs grow v-model="currentTab" :style="computedTabsStyle">
        <va-tab v-for="(tab, i) in tabs" :key="i">
          {{ $t('dashboard.support.tabs.' + tab) }}
        </va-tab>
      </va-tabs>
      <component
        :loading="loadingTabs.includes(tabs[currentTab])"
        :class="['', '', 'pb-4'][currentTab]"
        :socket="socket"
        :is="tabs[currentTab]"
        :messages="currentTab == 0 ? supportMessages : generalMessages"
      />
    </div>
  </va-card>
</template>

<script>
import SupportTab from './dashboard-support-tabs/SupportTab'
import GeneralChat from './dashboard-support-tabs/GeneralChat'
import { mapGetters } from 'vuex'

export default {
  name: 'dashboard-support',
  components: { SupportTab, GeneralChat },
  data: () => ({
    loadingTabs: [],
    tabs: ['support-tab', 'general-chat'],
    currentTab: 0,
    supportMessages: [],
    generalMessages: [],
  }),
  computed: {
    ...mapGetters({
      profile: 'profile',
      admin: 'user',
      socket: 'socket',
    }),
    computedTitle() {
      if (this.profile?.email)
        return (
          this.$t('dashboard.support.title-with') + ` ${this.profile.email}`
        )
      else return this.$t('dashboard.support.title')
    },
    computedStyle() {
      return {
        color: this.$themes.brightForeground,
        opacity: this.loading ? 0.2 : 1,
      }
    },
    computedWidgetStyle() {
      return {
        color: this.$themes.brightForeground,
      }
    },
    computedTabsStyle() {
      return {
        color: this.$themes.secondary,
      }
    },
  },
  watch: {
    socket: {
      immediate: true,
      handler() {
        if (!this.socket) return

        this.socket.on('set-general-messages', this.updateMessages)
        this.socket.on('support-message', this.supportMessageReceived)
        this.socket.on('general-message', this.generalMessageReceived)
      },
    },
  },
  methods: {
    profileOpened(profile) {
      this.updateSupportMessages(profile)
      this.getGeneralMessages(profile)
    },
    updateSupportMessages(profile) {
      if (!profile || profile.role != 'user') return
      this.supportMessages = profile.messages
    },
    getGeneralMessages(profile) {
      if (!profile || profile.role != 'user') return
      this.socket.emit('get-general-messages', profile.id)
    },
    updateMessages(messages) {
      this.generalMessages = messages
      this.$root.$emit('chat-update')
    },
    supportMessageReceived(message) {
      if (message.user != this.profile.id) {
        this.$root.$emit('support-message', message.user)
        return
      }

      this.supportMessages.push(message)
      this.$root.$emit('chat-update', message.yours)
    },
    generalMessageReceived(message) {
      this.generalMessages.push(message)
      this.$root.$emit('general-chat-update', message.yours)
    },
  },
  mounted() {
    this.$root.$on('open-profile', this.profileOpened)
  },
  beforeDestroy() {
    this.$root.$off('open-profile', this.profileOpened)
  },
}
</script>

<style lang="scss" scoped>
.tab {
  width: 100%;
  height: 100%;
  margin: -0.7rem;
}
</style>
