<template>
  <div class="app-page-layout" :style="computedStyle">
    <slot></slot>
    <slot name="content" />
  </div>
</template>

<script>
export default {
  name: 'app-page-layout',
  props: {
    mobileWidth: {
      type: Number,
      default: 767,
    },
  },
  data: () => ({
    isDesktop: null,
  }),
  computed: {
    computedStyle() {
      return { backgroundColor: this.$themes.darkBackground }
    },
  },
  created() {
    window.addEventListener('resize', this.checkIsDesktop)
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.checkIsDesktop)
  },
  mounted() {
    this.$emit('update:minimized', true)
    this.checkIsDesktop()
  },
  methods: {
    checkIsDesktop() {
      let updated = window.matchMedia(`(min-width: ${this.mobileWidth}px)`)
        .matches

      if (updated != this.isDesktop) {
        this.isDesktop = updated
        this.$root.$emit('resize', updated)
        this.$emit('switched-view', this.isDesktop ? 'desktop' : 'mobile')
        this.$store.commit('IT_IS_DESKTOP', updated)
      }
    },
  },
}
</script>

<style lang="scss">
.app-page-layout {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  height: 100vh;

  .content-wrap {
    transition: margin-left 0.3s ease;
    padding: 0;
    margin-left: 0 !important;
  }
}
</style>
