import axios from 'axios'
import store from '../store'

const base = store.getters.BASE

const cancelTokenSource = axios.CancelToken.source()
const cancelToken = cancelTokenSource.token

const abort = () => {
  cancelTokenSource.cancel()
}

const fetchMessages = (user, read) =>
  axios.get(
    base + '/api/admin/support?user=' + user + (read ? '&read=true' : ''),
    {
      cancelToken,
      headers: {
        Authorization: store.getters.token
      }
    }
  )
const sendMessage = (user, message) =>
  axios.post(
    base + '/api/admin/support/' + user + '/send',
    {
      message,
    },
    {
      cancelToken,
      headers: {
        Authorization: store.getters.token
      }
    }
  )

const updateMessages = (messages, user) =>
  axios.post(
    base + '/api/admin/support/update',
    {
      messages,
      user,
    },
    {
      cancelToken,
      headers: {
        Authorization: store.getters.token
      }
    }
  )

export { fetchMessages, sendMessage, updateMessages, abort }
