import { io } from 'socket.io-client'

const state = {
  token: '',
  socket: null,
  user: {
    role: {
      name: 'owner',
    },
  },
}

const mutations = {
  AUTH(state, { user, token }) {
    state.user = user
    state.token = token
  },
  CONNECT(state) {
    if (!state.user) return

    state.socket?.disconnect()
    state.socket = io('/', {
      query: `admin=${state.user._id}`,
    })
  },
}

export default {
  state,
  mutations,
}
