<template>
  <div class="tab">
    <div class="tab__bind pt-3 pb-2">
      <va-input
        class="ma-0"
        v-model="userRequisite"
        :placeholder="$t('forms.bindUser.placeholder')"
        :messages="[$t('forms.bindUser.description')]"
      >
        <va-icon slot="prepend" color="gray" name="fa fa-link" />
        <va-button
          slot="append"
          style="margin-right: 0;"
          small
          @click="bindUser"
        >
          {{ $t('forms.bindUser.submit') }}
        </va-button>
      </va-input>
    </div>
  </div>
</template>

<script>
import * as Users from '@/api/Users'

export default {
  name: 'profile-bind-tab',
  data: () => ({
    userRequisite: '',
  }),
  methods: {
    bindUser() {
      this.loading = true

      Users.bind(this.userRequisite)
        .then(({data}) => {
          this.loading = false
          this.userRequisite = ''

          this.$root.$emit('update-users')

          this.showToast(data.message, {
            icon: 'fa-check',
            position: 'bottom-right',
            duration: 4000,
             type: 'success',
          })
        })
        .catch(({response}) => {
          let error = response ? response.data.message : 'Connection error'

          this.loading = false

          this.showToast(error,
            {
              icon: 'fa-times',
              position: 'bottom-right',
              duration: 4000,
              type: 'primary',
            }
          )
        })
    },
  },
  mounted() {
    console.log('mounted')
  },
}
</script>

<style lang="scss" scoped>
.no-data {
  min-height: 65px;
  padding-top: 0.5rem;
  font-size: 1.2rem;
  text-align: center;
}
</style>
