<template>
  <va-dropdown class="color-dropdown" offset="0, 13px">
    <va-icon-color
      slot="anchor"
      class="color-dropdown__icon"
      :color="contextConfig.invertedColor ? $themes.gray : 'white'"
    />
    <div
      :style="dropdownStyle"
      class="color-dropdown__content pl-4 pr-4 pt-2 pb-2"
    >
      <div class="color-dropdown__content-label">Theme:</div>
      <va-button-toggle
        outline
        small
        v-model="selectedTheme"
        :options="themeOptions"
        style="max-width: 100%;"
        class="mb-3 "
      />
      <div class="color-dropdown__content-label">Accent:</div>
      <va-button-toggle
        outline
        small
        v-model="selectedAccent"
        :options="accentOptions"
        style="max-width: 100%;"
        class="mb-3 "
      />
    </div>
  </va-dropdown>
</template>

<script>
import VaIconColor from '../../../../../iconset/VaIconColor'

export default {
  inject: ['contextConfig'],
  components: {
    VaIconColor,
  },
  computed: {
    selectedTheme: {
      get() {
        return this.contextConfig.name
      },
      set(themeName) {
        localStorage.setItem('dashboard-theme', themeName)
        this.$root.$emit('change-theme', themeName)
      },
    },
    selectedAccent: {
      get() {
        return this.contextConfig.accent
      },
      set(accentName) {
        localStorage.setItem('dashboard-accent', accentName)
        this.$root.$emit('change-accent', accentName)
      },
    },
    themeOptions() {
      return [
        {
          label: 'Original',
          value: 'original',
        },
        {
          label: 'Decent',
          value: 'decent',
        },
        {
          label: 'Nibiru',
          value: 'nibiru',
        },
        {
          label: 'Corporate',
          value: 'corporate',
        },
      ]
    },
    accentOptions() {
      return [
        {
          label: 'Blue',
          value: 'blue',
        },
        {
          label: 'Cyan',
          value: 'cyan',
        },
        {
          label: 'Lime',
          value: 'lime',
        },
        {
          label: 'Lilac',
          value: 'lilac',
        },
      ]
    },
    dropdownStyle() {
      return {
        color: this.$themes.brightForeground,
        'background-color': this.$themes.background,
        'box-shadow': '0 2px 3px 0 ' + this.$themes.foreground + '22',
      }
    },
  },
  mounted() {
    if (!localStorage.getItem('dashboard-theme')) {
      this.selectedTheme = 'original'
    }
    if (!localStorage.getItem('dashboard-accent')) {
      this.selectedAccent = 'blue'
    }
  },
}
</script>

