import axios from 'axios'
import store from '../store'

const base = store.getters.BASE + '/api/admin'

const cancelTokenSource = axios.CancelToken.source()
const cancelToken = cancelTokenSource.token

const abort = () => {
  cancelTokenSource.cancel()
}

const setCommission = percent =>
  axios.post(
    base + '/set_commission',
    {
      commission: percent,
    },
    {
      cancelToken,
      headers: {
        Authorization: store.getters.token
      }
    }
  )
const setWithdrawErrorTemplates = templates =>
  axios.post(
    base + '/withdraw_error_templates',
    {
      templates,
    },
    {
      cancelToken,
      headers: {
        Authorization: store.getters.token
      }
    }
  )
const setCustomDepositError = error =>
  axios.post(
    base + '/set_deposit_error',
    {
      error,
    },
    {
      cancelToken,
      headers: {
        Authorization: store.getters.token
      }
    }
  )
const setCustomWithdrawError = error =>
  axios.post(
    base + '/set_withdraw_error',
    {
      error,
    },
    {
      cancelToken,
      headers: {
        Authorization: store.getters.token
      }
    }
  )
const setCustomDepositMin = (amount, currency) =>
  axios.post(
    base + '/set_min',
    {
      amount,
      currency,
    },
    {
      cancelToken,
      headers: {
        Authorization: store.getters.token
      }
    }
  )
const requireConfirmationEmail = value =>
  axios.post(
    base + '/set_email_confirmation',
    {
      require_email: value,
    },
    {
      cancelToke,
      headers: {
        Authorization: store.getters.token
      }
    }
  )

const getPromoList = () => axios.get(base + '/promo', {
  cancelToken,
  headers: {
    Authorization: store.getters.token
  }
})

const createPromo = opts => axios.post(base + '/promo', opts, {
  cancelToken,
  headers: {
    Authorization: store.getters.token
  }
})

const deletePromo = opts => axios.post(base + '/promo/delete', opts, {
  cancelToken,
  headers: {
    Authorization: store.getters.token
  }
})

const getTerms = () => axios.get(base + '/terms', {
  cancelToken,
  headers: {
    Authorization: store.getters.token
  }
}
)
const getWithdrawErrorTemplates = axios.get(base + '/withdraw_error_templates', {
  cancelToken, headers: {
    Authorization: store.getters.token
  }
})
const setTerms = terms =>
  axios.post(base + '/terms', { terms }, { cancelToken, })
const setChatProfiles = profiles =>
  axios.post(base + '/chat-profiles', { profiles }, { cancelToken })

export {
  setCommission,
  setCustomDepositMin,
  setCustomDepositError,
  setWithdrawErrorTemplates,
  getWithdrawErrorTemplates,
  setCustomWithdrawError,
  requireConfirmationEmail,
  getPromoList,
  createPromo,
  deletePromo,
  getTerms,
  setTerms,
  setChatProfiles,
  abort,
}
