<template>
  <div class="dashboard">
    <div class="row row-equal">
      <div class="flex xl6 lg12 md12 sm12 xs12">
        <dashboard-profile />
      </div>
      <div class="flex xl6 lg12 md12 sm12 xs12">
        <dashboard-support />
      </div>
    </div>
    <div class="row row-equal">
      <div class="flex xl6 lg12 md12 sm12 xs12">
        <dashboard-users />
      </div>
      <div class="flex xl6 lg12 md12 sm12 xs12">
        <dashboard-total-log />
      </div>
    </div>
  </div>
</template>

<script>
import DashboardUsers from './DashboardUsers'
import DashboardProfile from './DashboardProfile'
import DashboardSupport from './DashboardSupport'
import DashboardTotalLog from './DashboardTotalLog'

export default {
  name: 'dashboard',
  components: {
    DashboardUsers,
    DashboardProfile,
    DashboardSupport,
    DashboardTotalLog,
  },
  data: () => ({
    isDesktop: true,
  }),
  methods: {
    checkIsDesktop() {
      let updated = window.matchMedia(`(min-width: ${this.mobileWidth}px)`)
        .matches

      if (updated != this.isDesktop) {
        this.isDesktop = updated
      }
    },
  },
  mounted() {
    this.checkIsDesktop()
  },
}
</script>

<style lang="scss">
.row-equal .flex {
  .va-card {
    height: 100%;
  }
}

.dashboard {
  .va-card {
    margin-bottom: 0 !important;
  }
}
</style>
