<template>
  <va-inner-loading :loading="loading">
    <div class="general-chat" :style="computedStyle">
      <div class="general-chat_container">
        <div ref="messagesBox" class="general-chat_container_messages-box">
          <div
            v-for="(msg, i) in messages"
            :key="i"
            :class="{ real: msg.real }"
            class="general-chat_container_messages-box_item"
          >
            <div
              @click="chooseProfile(msg.user)"
              class="general-chat_container_messages-box_item-pic"
            >
              <va-icon
                :name="'fa fa-hand-paper-o'"
                class="general-chat_container_messages-box_item-pic_choose-profile"
              ></va-icon>
              <img
                v-if="msg.user.pic"
                :src="msg.user.pic"
                :alt="msg.user.firstName"
              />
              <span v-else>{{ getAlt(msg.user) }}</span>
            </div>
            <div
              @click="reply(msg.user.firstName)"
              class="general-chat_container_messages-box_item-name"
            >
              {{ getNickname(msg.user) }}
            </div>
            <div class="general-chat_container_messages-box_item-date">
              {{ formatTime(msg.at) }}
            </div>
            <div class="general-chat_container_messages-box_item-text">
              {{ msg.text }}
            </div>
          </div>
        </div>
        <div class="general-chat_container-input">
          <div v-if="!input" class="general-chat_container-input_placeholder">
            Start typing...
          </div>
          <div
            ref="input"
            spellcheck="false"
            @keypress="handleInputKeypress"
            @input="updateInput"
            contenteditable
            class="general-chat_container-input_editor"
          ></div>
        </div>
      </div>
      <div class="general-chat_profiles">
        <div class="general-chat_profiles_list pt-3">
          <va-select
            :options="profilesNames"
            :value="currentChatProfileName"
            v-model="currentChatProfileName"
            @input="selectProfile"
            :label="$t('dashboard.support.chat-profiles')"
            no-clear
          />
          <div class="general-chat_profiles_list_avatars">
            <div
              v-for="(avatar, i) in avatars"
              :key="i"
              @click="selectAvatar(i)"
              :style="computePicStyle(i)"
              class="general-chat_profiles_list_avatars_item"
            >
              <img :src="avatar" />
            </div>
          </div>
          <va-input
            :placeholder="$t('dashboard.support.profile.firstname')"
            v-model="profileFirstname"
          ></va-input>
          <va-input
            :placeholder="$t('dashboard.support.profile.lastname')"
            v-model="profileLastname"
          ></va-input>
          <va-button color="info" @click="saveProfiles">
            {{ $t('dashboard.support.profile.save') }}
          </va-button>
        </div>
        <div class="general-chat_profiles_avatar"></div>
        <div class="general-chat_profiles_name"></div>
      </div>
    </div>
  </va-inner-loading>
</template>

<script>
import moment from 'moment'
import { mapGetters } from 'vuex'

import { setChatProfiles } from '@/api/Settings'

const _ = require('lodash')

export default {
  name: 'general-chat',
  props: {
    messages: {
      type: Array,
      default: () => [],
    },
  },
  data: () => ({
    loading: false,
    input: '',
    profileFirstname: '',
    profileLastname: '',
    replyingTo: '',
    chatProfiles: [],
    currentChatProfileName: '',
    selectedAvatar: -1,
    avatars: [
      'https://i.ibb.co/yycbt3F/USDT.jpg',
      'https://i.ibb.co/jytq3hH/XRP.jpg',
      'https://i.ibb.co/d0xVzD0/BNB.jpg',
      'https://i.ibb.co/Sv7cqvB/BTC.jpg',
      'https://i.ibb.co/wNZW3Jd/ETH.jpg',
      'https://i.ibb.co/Mkmkmw8/LTC.jpg',
    ],
  }),
  computed: {
    ...mapGetters({
      profile: 'profile',
      user: 'user',
      socket: 'socket',
    }),
    currentChatProfile() {
      const filtered = this.chatProfiles.filter(
        p =>
          (p.firstName + ' ' + p.lastName).replace(/(^\s|\s$)/, '') ==
          this.currentChatProfileName
      )

      return filtered
        ? filtered[0]
        : {
            firstName: 'anonimous',
          }
    },
    profilesNames() {
      return [
        ...this.chatProfiles.map(p =>
          (p.firstName + ' ' + p.lastName).replace(/(^\s|\s$)/, '')
        ),
        'New profile',
      ]
    },
    computedStyle() {
      return {
        color: this.$themes.brightForeground,
        opacity: this.loading ? 0.2 : 1,
      }
    },
    fullName() {
      const firstName = this.profileFirstname || ''
      const lastName = this.profileLastname || ''
      return (firstName + ' ' + lastName).replace(/(^\s|\s$)/, '')
    },
  },
  watch: {
    chatProfiles: {
      immediate: true,
      handler(v) {
        if (v?.length) {
          this.currentChatProfileName = (
            v[0].firstName +
            ' ' +
            v[0].lastName
          ).replace(/(^\s|\s$)/, '')
          this.profileFirstname = v[0].firstName
          this.profileLastname = v[0].lastName || ''
        } else {
          this.currentChatProfileName = 'New profile'
        }
      },
    },
  },
  methods: {
    send() {
      const input = this.cleanedInput()

      if (!this.currentChatProfile || !input) return

      this.socket.emit('message', {
        userId: this.profile.id,
        message: {
          text: input,
          at: +new Date(),
          user: this.currentChatProfile,
        },
      })

      this.clear()
      this.scrollToBottom(true)
      this.focus()
    },
    saveProfiles() {
      const firstName = this.profileFirstname || ''
      const lastName = this.profileLastname || ''
      const name = this.fullName
      const pic = this.avatars[this.selectedAvatar] || ''

      if (name) {
        if (this.currentChatProfileName == 'New profile') {
          if (this.profilesNames.includes(name)) {
            this.showToast('Profile exists', {
              icon: 'fa-times',
              position: 'bottom-center',
              duration: 4000,
              type: 'primary',
            })
          } else {
            this.chatProfiles.unshift({
              firstName,
              lastName,
              pic,
            })

            setTimeout(() => {
              this.currentChatProfileName = name
            })
          }
        } else {
          const current = _.indexOf(
            this.chatProfiles.map(p =>
              (p.firstName + ' ' + p.lastName).replace(/(^\s|\s$)/, '')
            ),
            this.currentChatProfileName
          )

          if (
            this.profilesNames.includes(name) &&
            this.chatProfiles[current].pic == this.avatars[this.selectedAvatar]
          ) {
            this.showToast('Profile exists', {
              icon: 'fa-times',
              position: 'bottom-center',
              duration: 4000,
              type: 'primary',
            })
          } else {
            this.chatProfiles[current].firstName = firstName
            this.chatProfiles[current].lastName = lastName
            this.chatProfiles[current].pic = pic

            this.currentChatProfileName = name
          }
        }
      }

      setChatProfiles(this.chatProfiles)
        .then(() => {
          this.showToast('Profiles updated!', {
            icon: 'fa-check',
            position: 'bottom-center',
            duration: 4000,
            type: 'success',
          })
        })
        .catch(({ response }) => {
          if (response) {
            this.showToast(response.data.message, {
              icon: 'fa-times',
              position: 'bottom-center',
              duration: 4000,
              type: 'primary',
            })
          }
        })
    },
    selectProfile(name) {
      const current = _.indexOf(
        this.chatProfiles.map(p =>
          (p.firstName + ' ' + p.lastName).replace(/(^\s|\s$)/, '')
        ),
        name
      )

      if (current == -1) return

      this.profileFirstname = this.chatProfiles[current].firstName || ''
      this.profileLastname = this.chatProfiles[current].lastName || ''
      this.selectedAvatar = _.indexOf(
        this.avatars,
        this.chatProfiles[current].pic
      )
    },
    selectAvatar(i) {
      this.selectedAvatar = i
    },
    handleInputKeypress(e) {
      if (e.charCode == 13 || e.keyCode == 13 || e.which == 13) {
        e.preventDefault()
        this.send()
        return
      } else if (
        (e.charCode == 10 || e.keyCode == 10 || e.which == 10) &&
        e.ctrlKey
      ) {
        e.preventDefault()
        this.enterNewLine()
      }
    },
    enterNewLine() {
      document.execCommand('insertHTML', false, '<br>')
      this.updateInput()
    },
    clear() {
      this.$refs.input.innerHTML = ''
      this.updateInput()
    },
    focus() {
      this.$refs.input.focus()
    },
    caretToEnd(el) {
      var range = document.createRange()
      var sel = window.getSelection()

      range.setStart(el, 2)
      range.collapse(true)

      sel.removeAllRanges()
      sel.addRange(range)
    },
    reply(name) {
      let prev = this.replyingTo
      this.replyingTo = name + ', '
      this.$refs.input.innerHTML =
        this.replyingTo +
        this.input.slice(prev.length, this.input.length) +
        '&nbsp;<br>'
      this.updateInput()
      this.focus()
      this.caretToEnd(this.$refs.input)
    },
    chooseProfile(user) {
      if (!this.profilesNames.includes(user.firstName)) {
        let t = this
        this.chatProfiles.push(user)
        setTimeout(() => {
          t.currentChatProfileName = user.firstName
        })
      } else {
        this.currentChatProfileName = user.firstName
      }
    },
    cleanedInput() {
      return this.$refs.input.innerText
        .replace(/<br>/g, '\n')
        .replace(/^[\n\s]+|[\n\s]+$/g, '')
    },
    updateInput() {
      this.input = this.cleanedInput()
    },
    handlePasteEvent(e) {
      const map = {
        '&': '&amp;',
        '<': '&lt;',
        '>': '&gt;',
        '"': '&quot;',
        "'": '&#039;',
      }

      const escape_text = text => text.replace(/[&<>"']/g, m => map[m])

      e.preventDefault()
      var text = (e.originalEvent || e).clipboardData.getData('text/plain')
      document.execCommand('insertHtml', false, escape_text(text))
    },
    formatTime(ts) {
      return moment(ts).format('LT')
    },
    getAlt(user) {
      let fer = user.firstName[0].toUpperCase()
      let sec = user.lastName
        ? user.lastName[0].toUpperCase()
        : user.firstName[1].toUpperCase()

      return fer + sec
    },
    getNickname(user) {
      return (
        user.firstName.split('@')[0] +
        (user.lastName ? ' ' + user.lastName : '')
      )
    },
    computePicStyle(i) {
      const color = this.$themes.primary
      const selected = this.selectedAvatar == i
      return {
        border: selected ? '4px solid ' + color : '1px solid ' + color,
        opacity: selected ? 1 : 0.5,
      }
    },
    updateMessages(messages) {
      this.messages = messages
    },
    scrollToBottom(force) {
      const el = this.$refs.messagesBox
      if (force || this.reachedBottom()) {
        setTimeout(() => {
          el.scrollTop = el.scrollHeight
        })
      }
    },
    reachedBottom() {
      const el = this.$refs.messagesBox
      return el.scrollTop == el.scrollHeight - el.clientHeight
    },
    gettingDialogue() {
      this.loading = true
    },
    dialogueLoaded() {
      this.loading = false
    },
  },
  mounted() {
    this.chatProfiles = this.user.role?.settings['chat-profiles'] || []
    this.scrollToBottom()
  },
  created() {
    this.$root.$on('open-dialogue', this.dialogueLoaded)
    this.$root.$on('getting-dialogue', this.gettingDialogue)
    this.$root.$on('general-chat-update', this.scrollToBottom)
  },
  beforeDestroy() {
    this.$root.$off('open-dialogue', this.dialogueLoaded)
    this.$root.$off('getting-dialogue', this.gettingDialogue)
    this.$root.$off('general-chat-update', this.scrollToBottom)
  },
}
</script>

<style lang="scss">
@import '../../../sass/smart-grid';

.general-chat {
  @include to(40rem) {
    display: flex;
    gap: 25px;
    flex-direction: column-reverse;
  }
  @include from(40rem) {
    display: grid;
    gap: 15px;
    grid-template-columns: 1fr 1fr;
  }

  &_container {
    position: relative;
    padding-bottom: 60px;
    overflow: hidden;

    &-input {
      &_editor {
        width: 100%;
        min-height: 60px;
        background-color: #00000077;
        backdrop-filter: blur(10px);
        padding: 15px;
        &:focus {
          outline: none;
        }
      }
      &_placeholder {
        z-index: 10;
        pointer-events: none;

        position: absolute;
        top: 15px;
        left: 15px;
        opacity: 0.5;
      }
      position: absolute;
      box-shadow: 0 0 15px 5px #00000055;
      bottom: 0;
      min-height: 60px;
      width: 100%;
      left: 0;
      max-height: 200px;
      text-align: left;
      overflow-y: auto;
    }
    &_messages-box {
      min-height: 20rem;
      max-height: 30rem;
      overflow-y: auto;
      overflow-x: hidden;
      background-color: #00000022;
      display: flex;
      flex-direction: column;

      &_item {
        padding: 17px;
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        border-top: 1px solid #00000088;
        &.real {
          background-color: rgba(255, 255, 255, 0.1);
        }
        &:hover {
          background-color: #00000044;
        }

        &-pic {
          overflow: hidden;
          width: 30px;
          height: 30px;
          border-radius: 100%;
          display: flex;
          justify-content: center;
          border: 2px solid;
          align-items: center;
          font-size: 0.6rem;
          font-weight: 800;
          background-color: #00000033;
          cursor: pointer;
          transition: all 0.4s;
          position: relative;

          img {
            transition: all 0.4s;
            height: 100%;
          }

          span {
            transition: all 0.4s;
            user-select: none;
            pointer-events: none;
          }

          &_choose-profile {
            transition: all 0.4s;
            opacity: 0;
            position: absolute;
            font-size: 1.3rem;
          }

          &:hover {
            border: 2px solid blueviolet;
            .general-chat_container_messages-box_item-pic_choose-profile {
              opacity: 1;
            }
            img,
            span {
              opacity: 0;
            }
          }
        }
        &-date {
          display: grid;
          align-content: center;
          margin-top: 3px;
          opacity: 0.4;
          text-indent: 0.4rem;
        }
        &-name {
          display: grid;
          align-content: center;
          text-indent: 0.4rem;
          font-weight: 500;
          cursor: pointer;

          &:hover {
            color: #ffffff88;
          }
        }
        &-text {
          width: 100%;
        }
      }
    }
  }

  &_profiles {
    &_list {
      &_avatars {
        display: flex;
        flex-wrap: wrap;
        margin: 15px 0;
        gap: 5px;
        &::before {
          content: 'Avatar:';
          margin-right: 10px;
        }
        &_item {
          cursor: pointer;
          width: 50px;
          height: 50px;
          overflow: hidden;
          border-radius: 100%;
          transition: all 0.2s;
          img {
            user-select: none;
            margin-top: -2px;
            margin-left: -2px;
            width: 106%;
            &:hover {
              opacity: 0.6;
            }
          }
        }
      }
    }
  }
}
</style>
