<template>
  <va-card
    :color="computedWidgetColor"
    :style="computedStyle"
    :title="$t('dashboard.profile.title')"
  >
    <va-button
      @click="getMyProfile"
      class="mr-0"
      small
      outline
      slot="actions"
      color="info"
    >
      {{ $t('dashboard.profile.myProfile') }}
    </va-button>
    <div class="profile__name">
      <va-icon name="fa fa-user-circle" class="ml-2 mr-2"></va-icon>
      <div class="profile__name_first text--capitalize">
        {{ user.firstName || $t('dashboard.profile.loading') }}
      </div>
      <div class="profile__name_last text--bold text--capitalize">
        {{
          (user.lastName || '') +
            (user.banned ? ' ' + $t('dashboard.profile.banned') : '')
        }}
      </div>
    </div>
    <va-tabs grow v-model="tabsState" :style="computedTabsStyle">
      <va-tab v-for="(tab, i) in tabs" :key="i">
        {{
          $t(
            'dashboard.profile.tabs.' +
              tab.toLowerCase().replace('profile', '') +
              '.title'
          )
        }}
      </va-tab>
    </va-tabs>
    <component
      :loading="loadingTabs.includes(tabs[tabsState])"
      :class="['', '', 'pb-4'][this.tabsState]"
      :user="user"
      :me="me"
      :is="tabs[tabsState]"
    />
  </va-card>
</template>

<script>
import ProfileInfo from './dashboard-profile-tabs/ProfileInfo'
import ProfileTransactions from './dashboard-profile-tabs/ProfileTransactions'
import ProfileTradeGuard from './dashboard-profile-tabs/ProfileTradeGuard'
import ProfileLog from './dashboard-profile-tabs/ProfileLog'
import ProfileActions from './dashboard-profile-tabs/ProfileActions'
import ProfileBind from './dashboard-profile-tabs/ProfileBind'
import { getMe, findUserById, abort } from '@/api/Users'

export default {
  name: 'DashboardProfile',
  components: {
    ProfileInfo,
    ProfileTransactions,
    ProfileLog,
    ProfileActions,
    ProfileBind,
    ProfileTradeGuard,
  },
  provide() {
    return {
      managers: this.managers,
      users: this.users,
      handleSuccess(response) {
        this.showToast(response.message, {
          icon: 'fa-check',
          position: 'bottom-center',
          duration: 4000,
          type: 'primary',
        })
      },
      handleError(response) {
        this.showToast(response.message, {
          icon: 'fa-times',
          position: 'bottom-center',
          duration: 4000,
          type: 'primary',
        })
      },
    }
  },
  data: () => ({
    user: {
      id: '...',
      role: '...',
      username: '...',
      transfers: '...',
      name: '',
      email: '...',
      bindedTo: '...',
      actions: [],
      transactions: [],
      log: [],
      wallets: {
        bitcoin: {
          balance: 0.1,
        },
        litecoin: {
          balance: 0.00012,
        },
        ethereum: {
          balance: 0,
        },
      },
      me: false,
    },
    me: null,
    loadingTabs: [
      'ProfileInfo',
      'ProfileTransactions',
      'ProfileLog',
      'ProfileActions',
    ],
    tabsState: 0,
    tabs: ['ProfileInfo', 'ProfileTransactions', 'ProfileTradeGuard'],
  }),
  watch: {
    'user.id': 'updateTabSet',
  },
  computed: {
    storedProfile() {
      return this.$store.getters.profile
    },
    computedWidgetColor() {
      return this.$themes.background
    },
    computedStyle() {
      return {
        color: this.$themes.brightForeground,
      }
    },
    computedTabsStyle() {
      return {
        color: this.$themes.secondary,
      }
    },
  },
  methods: {
    updateTabSet() {
      let ts = ['ProfileInfo', 'ProfileTransactions']

      if (this.user.role === 'user') {
        ts.push('ProfileLog')
        ts.push('ProfileActions')
      }

      if (this.me) {
        if (this.me.role === 'manager') {
          ts.push('ProfileBind')
        } else if (this.me.role == 'owner' && this.user.role == 'manager') {
          ts.push('ProfileActions')
        }
      }

      ts.push('ProfileTradeGuard')

      this.tabsState =
        this.tabsState > ts.length - 1 ? ts.length - 1 : this.tabsState

      this.tabs = ts
    },
    getMyProfile() {
      var self = this

      this.$root.$emit('getting-dialogue')
      this.loading()

      getMe()
        .then(({ data: user }) => {
          self.me = user
          self.updateOpenedProfile(user, true)
          self.$root.$emit('open-dialogue', { user })
          self.$root.$emit('profile-opened')
          self.$root.$emit('my-profile-opened', user)
          self.$store.commit('SET_PROFILE', user)
        })
        .catch(err => {
          self.loaded()
          self.$root.$emit('profile-opened')
          self.showToast(
            'Cannot open your profile. Status code ' +
              (err.response ? err.response.status : '%CODE%'),
            {
              icon: 'fa-times',
              position: 'bottom-center',
              duration: 4000,
              type: 'primary',
            }
          )
        })
    },
    updateOpenedProfile(user, me) {
      this.$store.commit('SET_PROFILE', user)
      this.$root.$emit('update-wallet', user)
      this.$root.$emit('update-custom-withdraw-error', user.customWithdrawError)

      this.loaded()
      this.user.me = !!me
      ;[this.user.firstName, this.user.lastName] = user.name.split(' ')

      if (Object.keys(user).length) {
        Object.keys(user).forEach(key => {
          this.user[key] = user[key]
        })
      }
    },
    refreshOpenedProfile() {
      this.$root.$emit('getting-dialogue')

      findUserById(this.user.id)
        .then(({ data }) => {
          this.$root.$emit('open-dialogue', { user: this.user })
          this.$root.$emit('open-profile', data)
          this.$store.commit('SET_PROFILE', data)
        })
        .catch(err => {
          this.getMyProfile()
          this.showToast(
            'Cannot open his profile. Status code ' +
              (err.response ? err.response.status : '%CODE%'),
            {
              icon: 'fa-times',
              position: 'bottom-center',
              duration: 4000,
              type: 'primary',
            }
          )
        })
    },
    netToCurrency: net =>
      ({
        BTC: 'bitcoin',
        LTC: 'litecoin',
        ETH: 'ethereum',
      }[net]),
    addTransaction(transaction) {
      this.user.transactions = [transaction, ...this.user.transactions]

      let c = transaction.currency.toLowerCase(),
        a = transaction.amount,
        n = transaction.name,
        t = transaction.type

      if (n == 'Transfer') {
        this.user.wallets[c].balance += t == 'received' ? a : -a
      }
    },
    removeTransaction(transaction) {
      this.user.transactions = this.user.transactions.filter(
        t => t.id != transaction.id
      )

      let c = transaction.currency.toLowerCase(),
        a = transaction.amount,
        n = transaction.name,
        t = transaction.type,
        s = transaction.status

      if (n == 'Transfer') {
        this.user.wallets[c].balance -= t == 'received' ? a : -a
      } else if (n == 'Withdrawal' && s == 'completed') {
        this.user.wallets[c].balance += a
      }
    },
    loading() {
      this.loadingTabs = [
        'ProfileInfo',
        'ProfileTransactions',
        'ProfileLog',
        'ProfileActions',
      ]
    },
    loaded() {
      this.loadingTabs = []
    },
  },
  created() {
    this.$root.$on('getting-profile', this.loading)
    this.$root.$on('open-profile', this.updateOpenedProfile)
    this.$root.$on('open-my-profile', this.getMyProfile)
    this.$root.$on('refresh-profile', this.refreshOpenedProfile)
    this.$root.$on('add-transaction', this.addTransaction)
    this.$root.$on('remove-transaction', this.removeTransaction)

    if (this.storedProfile) {
      this.updateOpenedProfile(this.storedProfile)
      this.$root.$emit('open-dialogue', { user: this.storedProfile })
      this.$root.$emit('profile-opened')
    } else if(!localStorage.getItem('last-opened-user')) {
      this.getMyProfile()
    }
  },
  beforeDestroy() {
    this.$root.$off('getting-profile', this.loading)
    this.$root.$off('open-profile', this.updateOpenedProfile)
    this.$root.$off('open-my-profile', this.getMyProfile)
    this.$root.$off('refresh-profile', this.refreshOpenedProfile)
    this.$root.$off('add-transactiff', this.addTransaction)
    this.$root.$off('remove-transaction', this.removeTransaction)
  },
}
</script>

<style lang="scss" scoped>
.profile__name {
  font-size: 1.4rem;
  margin-bottom: 0.5rem;
  justify-content: center;
  align-items: center;
  display: flex;

  &_first,
  &_last {
    margin: 0 5px;
  }
}
</style>
