<template>
  <app-page-layout
    class="app-layout"
    :is-top-bar.sync="isTopBar"
    :minimized.sync="minimized"
    :mobile-width="mobileWidth"
    @switched-view="fitNavbar"
  >
    <app-navbar
      class="app-layout__navbar"
      :is-top-bar.sync="isTopBar"
      :minimized.sync="minimized"
    />
    <app-topbar class="app-layout__topbar" v-if="isTopBar" />
    <div class="app-layout__container">
      <app-sidebar
        class="app-layout__sidebar"
        v-if="!isTopBar"
        :minimized="minimized"
      />
      <div
        class="app-layout__main"
        :class="{ 'app-layout__main--top': isTopBar }"
      >
        <main
          :class="
            'app-layout__main-layout layout fluid gutter--' +
              (isDesktop ? 'xl' : 'md')
          "
          slot="content"
          role="main"
        >
          <router-view />
        </main>
      </div>
    </div>
  </app-page-layout>
</template>

<script>
import AppPageLayout from './AppPageLayout'
import AppNavbar from './app-navbar/AppNavbar'
import AppTopbar from './app-topbar/AppTopbar'
import AppSidebar from './app-sidebar/AppSidebar'
import * as Themes from '../../services/themes-config'
import ThemeAccents from '../../services/themes-config/accents'
import {
  ColorThemeActionsMixin,
  ColorThemeMixin,
} from '../../services/vuestic-ui'

import { mapGetters } from 'vuex'

export default {
  name: 'app-layout',
  components: {
    AppPageLayout,
    AppNavbar,
    AppTopbar,
    AppSidebar,
  },
  data() {
    return {
      isTopBar: false,
      minimized: true,
      mobileWidth: 600,
      isDesktop: true,
    }
  },
  computed: {
    ...mapGetters({
      user: 'user',
    }),
  },
  inject: ['contextConfig'],
  mixins: [ColorThemeActionsMixin, ColorThemeMixin],
  created() {
    let theme = localStorage.getItem('dashboard-theme')
    let accent = localStorage.getItem('dashboard-accent')

    this.setTheme(theme)
    this.setAccent(accent)

    this.$root.$on('change-theme', this.setTheme)
    this.$root.$on('change-accent', this.setAccent)
  },
  mounted() {
    this.$store.commit('CONNECT')
  },
  beforeDestroy() {
    this.$root.$off('change-theme', this.setTheme)
    this.$root.$off('change-accent', this.setAccent)
  },
  methods: {
    fitNavbar(view) {
      this.isDesktop = view == 'desktop'

      if (view == 'mobile') {
        localStorage.setItem('dashboard-topbar', true)
        this.isTopBar = true
      }
    },
    setTheme(themeName) {
      themeName = themeName ? themeName : 'original'

      const theme = Themes[themeName + 'Theme']

      this.setColors(theme.colors)

      Object.keys(theme.context).forEach(key => {
        this.contextConfig[key] = theme.context[key]
      })

      this.setAccent(localStorage.getItem('dashboard-accent'))
    },
    setAccent(name) {
      name = name ? name : 'blue'

      const accent = ThemeAccents[name]

      this.setColors(accent)

      Object.keys(accent).forEach(key => {
        this.contextConfig[key] = accent[key]
      })
    },
  },
}
</script>

<style lang="scss">
.app-layout {
  display: flex;
  flex-direction: column;

  &__container {
    display: flex;
    flex-wrap: nowrap;
    align-items: stretch;
    // TODO Probably there is a better way to achieve this.
    height: calc(100% - 65px);

    @include media-breakpoint-down(sm) {
      height: calc(100% - 110px);
    }
  }

  &__main {
    box-sizing: border-box;
    width: 100%;
    position: relative;
    max-height: 100%;
    min-height: 100%;

    &-layout {
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
      overflow: auto;
      box-sizing: border-box;
      min-height: 100%;
      margin: 0;
    }
  }
}
</style>
