const state = {
  users: null,
  profile: null,
  promo: [],
  logs: [],
  users: [],
  deposits: [],
}

const mutations = {
  AUTHS(state, users) {
    state.users = users
  },
  SET_PROFILE(state, profile) {
    state.profile = profile
  },
  SET_PROMO(state, data) {
    state.promo = data
  },
  ADD_PROMO(state, el) {
    state.promo.unshift(el)
  },
  SET_USERS(state, users) {
    state.users = users
  },
  SET_DEPOSITS(state, deposits) {
    state.deposits = deposits
  },
}

const getters = {
  promo: state => state.promo,
  managers: state =>
    state.users
      ? state.users.filter(u => ['manager', 'owner'].includes(u.role))
      : [],
}

export default {
  state,
  mutations,
  getters,
}
