<template>
  <div class="support-tab">
    <va-inner-loading :loading="!itsAdmin && loading">
      <div v-if="!itsAdmin" class="tab" :style="computedStyle">
        <chat
          @update-messages="updateMessages"
          :value="messages"
          @input="sendMessage"
          @read-dialogue="readDialogue"
          @delete-attachment="deleteImage"
        />
      </div>
      <va-card-no-data
        v-else
        :loading="loading"
        :loading-text="$t('dashboard.no-data.support.loading')"
        :empty-text="$t('dashboard.no-data.support.empty')"
      />
    </va-inner-loading>
  </div>
</template>

<script>
import Chat from '@/components/ui/chat/Chat'
import VaCardNoData from '@/components/dashboard/components/VaCardNoData'
import * as Support from '@/api/Support'

import { mapGetters } from 'vuex'

export default {
  name: 'support-tab',
  components: {
    Chat,
    VaCardNoData,
  },
  props: {
    messages: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      user: {
        id: '',
        name: 'heyy',
        messages: [],
      },
      itsAdmin: true,
      loading: false,
      userRaw: null,
      prevTimeout: null,
    }
  },
  watch: {
    profile: {
      immediate: true,
      handler(v) {
        if (v) this.updateProfile(v)
      },
    },
  },
  computed: {
    ...mapGetters({
      profile: 'profile',
      socket: 'socket',
    }),
    computedStyle() {
      return {
        color: this.$themes.brightForeground,
        opacity: this.loading ? 0.2 : 1,
      }
    },
  },
  methods: {
    sendMessage(message) {
      this.socket.emit('support-message', {
        token: localStorage.getItem('auth-token'),
        userId: this.profile.id,
        message,
      })
    },
    updateMessages(messages) {
      Support.updateMessages(messages, this.user.id)
        .then(response => {
          this.showToast('The messages has been updated', {
            icon: 'fa-check',
            position: 'bottom-center',
            duration: 4000,
            type: 'success',
          })
        })
        .catch(err => {
          if (err.response?.data?.message) {
            this.showToast(err.response.data.message, {
              icon: 'fa-times',
              position: 'bottom-center',
              duration: 4000,
              type: 'primary',
            })
          } else {
            this.showToast('The messages has not been updated', {
              icon: 'fa-times',
              position: 'bottom-center',
              duration: 4000,
              type: 'primary',
            })
          }
        })
    },
    readDialogue() {
      this.loading = true
      Support.fetchMessages(this.user.id, true)
        .then(() => {
          this.loading = false
          if (this.userRaw) this.userRaw.unread = 0
          this.showToast('Dialogue has read', {
            icon: 'fa-check',
            position: 'bottom-center',
            duration: 4000,
            type: 'success',
          })
        })
        .catch(err => {
          this.loading = false
          this.showToast('Unexpected error', {
            icon: 'fa-times',
            position: 'bottom-center',
            duration: 4000,
            type: 'primary',
          })
        })
    },
    openDialogue({ user, messages }) {
      this.loading = false
      this.itsAdmin = user.role != 'user'
      this.user.id = user.id
      this.user.name = user.name
      this.userRaw = user

      if (!this.itsAdmin) {
        this.user.messages = messages
        this.$root.$emit('chat-update')
        clearTimeout(this.prevTimeout)
      }
    },
    gettingDialogue() {
      this.loading = true
    },
    deleteImage(ts) {
      this.user.messages.forEach(m => {
        if (m.date == ts) {
          m.image = undefined
        }
      })

      this.user.messages = this.user.messages.filter(m => m.text || m.image)

      this.updateMessages(this.user.messages)
    },
    startFetchingMessages() {
      var userWhenStarted = this.user.id

      Support.fetchMessages(userWhenStarted)
        .then(({ data }) => {
          if (userWhenStarted != this.user.id) return

          if (this.user.messages.length != data.messages.length) {
            var sending = this.user.messages.filter(m => !m.delivered)

            this.user.messages = [...data.messages, ...sending]
            this.$root.$emit('chat-update')
          }
        })
        .catch(err => {
          // this.prevTimeout = setTimeout(this.startFetchingMessages, 20000)
        })
    },
    updateProfile(user) {
      this.user = user
      this.openDialogue({ user, messages: user.messages })
    },
  },
  created() {
    this.$root.$on('open-dialogue', this.openDialogue)
    this.$root.$on('getting-dialogue', this.gettingDialogue)
  },
  beforeDestroy() {
    this.$root.$off('open-dialogue', this.openDialogue)
    this.$root.$off('getting-dialogue', this.gettingDialogue)

    clearTimeout(this.prevTimeout)
  },
}
</script>

<style lang="scss" scoped>
.tab {
  height: 100%;
}

.no-data {
  min-height: 65px;
  padding-top: 0.5rem;
  font-size: 1.2rem;
  text-align: center;
}

.user-title {
  font-size: 0.8rem;
  letter-spacing: 0.06rem;
  font-weight: 500;
  text-transform: uppercase;
}
</style>
