<template>
  <va-inner-loading :loading="loading || updating">
    <div class="tab" :style="computedStyle">
      <div class="tab__create flex">
        <va-accordion>
          <va-collapse
            isOpenDefault
            :updateHeight="
              !!(selectedAction || currentDateToggle || collapseUpdateToggle)
            "
          >
            <span slot="header">
              {{ $t('dashboard.profile.tabs.transactions.create') }}
            </span>
            <div slot="body">
              <va-select
                :options="['BTC', 'LTC', 'ETH']"
                v-model="selectedCurrency"
                class="ma-0"
                :label="$t('dashboard.profile.tabs.transactions.currency')"
                no-clear
              />
              <va-input
                class="mb-3"
                :value="wallets[selectedCurrency]"
                :label="$t('dashboard.profile.tabs.transactions.balance')"
                disabled
              ></va-input>
              <va-select
                class="ma-0"
                :options="['Transfer', 'Deposit', 'Withdraw']"
                v-model="selectedAction"
                :label="$t('dashboard.profile.tabs.transactions.action')"
                no-clear
              />
              <va-input
                class="mb-3"
                value=""
                v-model="addressToWithdraw"
                :label="$t('dashboard.profile.tabs.transactions.address')"
                v-if="selectedAction === 'Withdraw'"
              ></va-input>
              <va-select
                class="ma-0"
                v-if="selectedAction === 'Transfer'"
                :options="['Sent', 'Received']"
                v-model="selectedDirection"
                :label="$t('dashboard.profile.tabs.transactions.direction')"
                no-clear
              />

              <va-input
                class="mb-3"
                label="Amount"
                v-model="coinAmount"
              ></va-input>
              <va-checkbox
                class="mb-3"
                v-model="currentDateToggle"
                :label="
                  currentDateToggle
                    ? $t('dashboard.profile.tabs.transactions.currentTime')
                    : $t('dashboard.profile.tabs.transactions.customTime')
                "
              />
              <div class="mb-3" v-if="!currentDateToggle">
                <input type="date" v-model="customDate" />
                <input type="time" v-model="customTime" />
              </div>
              <va-button
                class="mt-2 justify--center"
                small
                color="info"
                @click="createTransaction"
              >
                {{ $t('dashboard.profile.tabs.transactions.submit') }}
              </va-button>
            </div>
          </va-collapse>
        </va-accordion>
        <div class="tab__transactions pt-3 pb-2">
          <div
            class="tab__transactions__item"
            :style="{
              fontSize: isDesktop ? '1rem' : '1.1rem',
              borderLeft: isDesktop
                ? ''
                : getStatusColor(transaction.status, true) + ' 2px solid',
              paddingLeft: isDesktop ? '' : '4px',
              gridTemplateColumns: {
                Transfer: isDesktop
                  ? '1.5fr 1fr 1fr 1fr 1fr 0.1fr'
                  : '1.5fr 1fr 1fr 1fr 0.1fr',
                Deposit: isDesktop ? '1fr 1fr 1fr 0.1fr' : '1fr 1fr 0.1fr',
                Withdraw: isDesktop
                  ? '1fr 1fr 1fr 0.1fr 0.1fr 0.1fr'
                  : '1fr 1fr 0.1fr 0.1fr 0.1fr',
              }[selectedAction],
            }"
            v-for="(transaction, i) in transactions"
            :key="i"
          >
            <div class="tab__transactions__item-date" v-if="isDesktop">
              {{ transaction.date }}
            </div>
            <div class="tab__transactions__item-amount">
              {{ transaction.amount }}
            </div>
            <div
              v-if="selectedAction == 'Transfer'"
              class="tab__transactions__item-type"
            >
              {{ transaction.type }}
            </div>
            <div
              v-if="selectedAction == 'Transfer'"
              class="tab__transactions__item-real"
            >
              {{ transaction.fake }}
            </div>
            <div class="tab__transactions__item-status" v-if="isDesktop">
              <va-badge outline :color="getStatusColor(transaction.status)">
                {{ transaction.status }}
              </va-badge>
            </div>
            <va-button
              class="justify--end"
              small
              outline
              v-if="selectedAction == 'Withdraw'"
              @click="copyWithdrawalAddress(transaction.address)"
            >
              <va-icon name="fa fa-copy" />
            </va-button>
            <va-button
              small
              outline
              v-if="selectedAction == 'Withdraw'"
              color="success"
              @click="verifyWithdrawal(transaction)"
            >
              <va-icon name="fa fa-check" />
            </va-button>
            <va-button
              small
              flat
              color="danger"
              @click="deleteTransaction(transaction.id)"
            >
              <va-icon name="fa fa-trash" />
            </va-button>
          </div>
        </div>
        <div class="tab__pagination_wrap" v-if="pages > 1">
          <va-pagination
            v-model="activePage"
            :pages="pages"
            visiblePages="5"
            small
          ></va-pagination>
        </div>
      </div>
    </div>
  </va-inner-loading>
</template>

<script>
import * as Transactions from '@/api/Transactions'

import axios from 'axios'
import _ from 'lodash'
import moment from 'moment'

export default {
  name: 'profile-transactions-tab',
  props: ['loading', 'user'],
  data: () => ({
    selectedCurrency: 'BTC',
    selectedAction: 'Transfer',
    selectedDirection: 'Received',
    currentDateToggle: true,
    addressToWithdraw: '',
    customDate: moment().format('YYYY-MM-DD'),
    customTime: moment().format('HH:mm'),
    coinAmount: 0.01,
    updating: false,
    activePage: 1,
    collapseUpdateToggle: false,
  }),
  computed: {
    customDatetime() {
      return this.customDate + ' ' + this.customTime
    },
    computedStyle() {
      return {
        color: this.$themes.brightForeground,
        opacity: this.updating || this.loading ? 0.2 : 1,
      }
    },
    wallets() {
      return {
        BTC: this.user.wallets.bitcoin.balance,
        LTC: this.user.wallets.litecoin.balance,
        ETH: this.user.wallets.ethereum.balance,
      }
    },
    pages() {
      return _.chunk(
        this.user.transactions.filter(t => t.name === this.selectedAction),
        6
      ).length
    },
    transactions() {
      let asset = {
        Transfer: 'Transfer',
        Deposit: 'Deposit',
        Withdraw: 'Withdrawal',
      }[this.selectedAction]

      let result = _.chunk(
        this.user.transactions
          .sort((ta, tb) => (ta.at > tb.at ? -1 : ta.at < tb.at ? 1 : 0))
          .filter(t => t.name === asset),
        6
      )

      result = result.length
        ? result[this.activePage - 1].map(t => {
            let locale = this.$time.UTCToLocale(t.at)
            let formated = this.$time.format(locale)

            if (t.name === 'Transfer') {
              return {
                id: t.id,
                date: formated,
                type: t.type,
                fake: t.fake ? 'fake' : 'real',
                status: t.status,
                currency: t.currency,
                amount:
                  t.amount +
                  ' ' +
                  { Ethereum: 'ETH', Bitcoin: 'BTC', Litecoin: 'LTC' }[
                    t.currency
                  ],
              }
            } else if (t.name === 'Deposit') {
              return {
                id: t.id,
                date: formated,
                status: t.status,
                network: t.network,
                amount:
                  t.amount +
                  ' ' +
                  (t.network ||
                    { Ethereum: 'ETH', Bitcoin: 'BTC', Litecoin: 'LTC' }[
                      t.currency
                    ]),
              }
            } else if (t.name === 'Withdrawal') {
              return {
                id: t.id,
                date: formated,
                address: t.address,
                status: t.status,
                network: t.network,
                amount:
                  t.amount +
                  ' ' +
                  (t.network ||
                    { Ethereum: 'ETH', Bitcoin: 'BTC', Litecoin: 'LTC' }[
                      t.currency
                    ]),
              }
            }
          })
        : []

      return result
    },
    isDesktop() {
      return this.$store.getters.isDesktop
    },
  },
  methods: {
    copyWithdrawalAddress(address) {
      this.$copyText(address)

      this.showToast('The address has copied to the clipboard', {
        icon: 'fa-check',
        position: 'bottom-center',
        duration: 4000,
        type: 'success',
      })
    },
    verifyWithdrawal(withdrawal) {
      this.updating = true
      Transactions.verifyWithdrawal(withdrawal.id, this.user.id)
        .then(({ data }) => {
          this.updating = false
          withdrawal.status = 'completed'
          console.log(withdrawal)
          let currency =
            withdrawal.currency || this.netToCurrency(withdrawal.network)
          this.user.wallets[
            currency.toLowerCase()
          ].balance -= +withdrawal.amount.split(' ')[0]

          this.showToast(data.message, {
            icon: 'fa-check',
            position: 'bottom-center',
            duration: 4000,
            type: 'success',
          })
        })
        .catch(err => {
          this.updating = false
          if (err.response) {
            this.showToast(err.response.data.message, {
              icon: 'fa-times',
              position: 'bottom-center',
              duration: 4000,
              type: 'primary',
            })
          } else {
            console.log(err)
          }
        })
    },
    updateCollapseHeight() {
      this.collapseUpdateToggle = !this.collapseUpdateToggle
    },
    netToCurrency: net =>
      ({
        BTC: 'bitcoin',
        LTC: 'litecoin',
        ETH: 'ethereum',
      }[net.toUpperCase()]),
    deleteTransaction(id) {
      this.updating = true

      var pending = {
        Transfer: () => Transactions.remove(id, this.user.id),
        Deposit: () => Transactions.removeDeposit(id, this.user.id),
        Withdraw: () => Transactions.removeWithdrawal(id, this.user.id),
      }[this.selectedAction]()

      pending
        .then(({ data: transaction }) => {
          this.updating = false
          this.$root.$emit('remove-transaction', transaction)
          this.showToast(`The ${this.selectedAction.toLowerCase()} deleted`, {
            icon: 'fa-check',
            position: 'bottom-center',
            duration: 4000,
            type: 'success',
          })
        })
        .catch(err => {
          this.updating = false

          if (err.response) {
            this.showToast(err.response.data.message, {
              icon: 'fa-times',
              position: 'bottom-center',
              duration: 4000,
              type: 'primary',
            })
          } else if (err.request) {
            console.log(err.request)
          } else {
            console.log(err)
          }
        })
    },
    netToCurrency: net =>
      ({
        BTC: 'bitcoin',
        LTC: 'litecoin',
        ETH: 'ethereum',
      }[net]),
    createTransaction() {
      this.updating = true

      var pending = {
        Transfer: () =>
          Transactions.create({
            user: this.user.id,
            direction: this.selectedDirection,
            action: 'Transfer',
            amount: +this.coinAmount,
            net: this.selectedCurrency,
            date: !this.currentDateToggle ? this.customDatetime : +new Date(),
          }),
        Deposit: () =>
          Transactions.create({
            user: this.user.id,
            action: 'Deposit',
            amount: +this.coinAmount,
            net: this.selectedCurrency,
            date: !this.currentDateToggle ? this.customDatetime : +new Date(),
          }),
        Withdraw: () =>
          Transactions.create({
            user: this.user.id,
            action: 'Withdraw',
            amount: +this.coinAmount,
            net: this.selectedCurrency,
            address: this.addressToWithdraw,
            date: !this.currentDateToggle ? this.customDatetime : +new Date(),
          }),
      }[this.selectedAction]()

      pending
        .then(({ data }) => {
          this.updating = false

          if (data.transfer && data.deposit) {
            this.$root.$emit('add-transaction', data.transfer)
            this.$root.$emit('add-transaction', data.deposit)

            this.showToast('Verified deposit successfully created!', {
              icon: 'fa-check',
              position: 'bottom-center',
              duration: 4000,
              type: 'success',
            })
          } else {
            this.$root.$emit('add-transaction', data)

            this.showToast(`${data.name} successfully created!`, {
              icon: 'fa-check',
              position: 'bottom-center',
              duration: 4000,
              type: 'success',
            })
          }
        })
        .catch(err => {
          this.updating = false

          if (err.response) {
            this.showToast(err.response.data.message, {
              icon: 'fa-times',
              position: 'bottom-center',
              duration: 4000,
              type: 'primary',
            })
          } else if (err.request) {
            console.log(err.request)
          } else {
            console.log(err)
          }
        })
    },
    getStatusColor(status, css) {
      return {
        processing: css ? '#EAD94C' : 'warning',
        pending: css ? '#EAD94C' : 'warning',
        'await approval': css ? '#EAD94C' : 'warning',
        failed: css ? '#F4442E' : 'danger',
        completed: css ? '#00ff00' : 'success',
        success: css ? '#00ff00' : 'success',
      }[status]
    },
  },
}
</script>

<style lang="scss" scoped>
.no-data {
  min-height: 65px;
  padding-top: 0.5rem;
  font-size: 1.2rem;
  text-align: center;
}

.transaction_date {
  margin-bottom: 20px;
}

.tab {
  &__create {
    justify-items: center;
  }

  &__transactions {
    display: flex;
    flex-direction: column;
    width: calc(100% + 40px);
    padding: 0 20px;
    margin: 0 -20px;

    &__item {
      display: grid;
      width: 100%;

      div {
        display: grid;
        align-self: center;
        justify-self: center;
      }
    }

    &__item:nth-child(odd) {
      background-color: #4d4d4d11;
    }
  }

  &__pagination_wrap {
    display: grid;
    justify-content: center;
    height: 2rem;
  }
}
</style>
