<template>
  <va-inner-loading :loading="loading && log.length">
    <div v-if="log.length" class="tab" :style="computedStyle">
      <div class="row">
        <div class="flex xs12 xl12">
          <div
            v-for="action in log[activePage - 1]"
            :key="action._id"
            class="tab__item d-flex align--center"
            :style="{
              padding: isDesktop ? '0 20px' : '0 10px',
              height: isDesktop ? '55px' : '40px',
              fontSize: isDesktop ? '1rem' : '.9rem'
            }"
          >
            <va-icon
              :name="actionIcon(action.actionName)"
              class="ml-3 mr-3"
            ></va-icon>
            <div>
              {{ $t(action.messageLocalPath) }}
            </div>
            <div style="opacity:.4;margin-left: 10px">
              {{ action.formatedDate }}
            </div>
          </div>
        </div>
      </div>
      <div class="tab__pagination_wrap" v-if="log.length > 1">
        <va-pagination
          v-model="activePage"
          :pages="log.length"
          visiblePages="5"
          small
        ></va-pagination>
      </div>
    </div>
    <VaCardNoData
        v-else
        :loading="loading"
        :loading-text="$t('dashboard.no-data.log.loading')"
        :empty-text="$t('dashboard.no-data.log.empty')"
      />
  </va-inner-loading>
</template>

<script>
import VaCardNoData from '../components/VaCardNoData'
import _ from 'lodash'

export default {
  name: 'profile-log-tab',
  components: {
    VaCardNoData,
  },
  data: () => ({
    activePage: 1,
  }),
  props: {
    loading: Boolean,
    user: Object
  },
  computed: {
    computedStyle() {
      return {
        color: this.$themes.brightForeground,
        opacity: this.loading ? 0.2 : 1,
      }
    },
    log() {
      let self = this
      this.user.log = this.user.log.map(action => ({
        ...action,
        formatedDate: action.fromNow || self.$time.format(action.at),
      }))

      return _.chunk(this.user.log, 6)
    },
    isDesktop() {
      return this.$store.getters.isDesktop
    },
  },
  methods: {
    actionIcon(name) {
      return {
        registered: 'fa fa-user-plus',
        authenticated: 'fa fa-sign-in',
        visited: 'fa fa-map-marker',
        password_changed: 'fa fa-edit',
        name_changed: 'fa fa-edit',
        transfer: 'fa fa-money',
        withdrawal: 'fa fa-money',
        deposit: 'fa fa-money',
      }[name]
    },
  },
}
</script>

<style lang="scss" scoped>
.tab {
  min-height: 232px;

  &__wrap {
    max-height: 400px;
  }

  &__item {
    height: 50px;
    width: calc(100% + 40px);
    padding: 0 20px;
    margin: 0 -20px;

    &-name {
      min-width: 80px;
      max-width: 80px;
    }

    &:nth-child(odd) {
      background-color: #ffffff07;
    }
  }

  &__pagination_wrap {
    display: grid;
    justify-content: center;
    height: 2rem;
  }
}
</style>
