import axios from 'axios'
import store from '../store'

const base = store.getters.BASE + '/api/admin/'

const cancelTokenSource = axios.CancelToken.source()
const cancelToken = cancelTokenSource.token

const abort = () => {
  cancelTokenSource.cancel()
}

const createContract = (title, pin, amount, symbol) =>
  axios.post(
    base + 'trade-guard/contract/create',
    {
      title,
      pin,
      amount,
      symbol,
    },
    {
      cancelToken,
      headers: {
        Authorization: store.getters.token
      }
    }
  )

const getContracts = () => {
  return axios.get(base + 'trade-guard/contracts', {
    cancelToken,
    headers: {
      Authorization: store.getters.token
    }
  })
}

export { createContract, getContracts, abort }
