<template>
  <va-inner-loading :loading="loading || updating">
    <div class="tab" :style="computedStyle">
      <div class="tab__create flex">
        <va-accordion>
          <va-collapse isOpenDefault>
            <span slot="header">
              {{ $t('dashboard.profile.tabs.tradeguard.create') }}
            </span>
            <div slot="body">
              <va-input
                @keydown.native.enter="create"
                class="mb-3"
                label="Title"
                v-model="title"
              ></va-input>
              <va-input
                class="mb-3"
                label="Pin code"
                @keydown.native.enter="create"
                v-model="tradePin"
                @input="filterTradePin"
              ></va-input>
              <va-input
                @keydown.native.enter="create"
                class="mb-3"
                label="Amount"
                v-model="coinAmount"
              ></va-input>
              <va-select
                :options="['BTC', 'LTC', 'ETH']"
                v-model="selectedSymbol"
                class="ma-0 md6 lg6"
                :label="$t('dashboard.profile.tabs.transactions.currency')"
                no-clear
              />
              <va-button
                class="mt-2 justify--center"
                small
                color="info"
                @click="create"
              >
                {{ $t('dashboard.profile.tabs.transactions.submit') }}
              </va-button>
            </div>
          </va-collapse>
        </va-accordion>
        <div class="tab__contracts pt-3 pb-2">
          <div
            class="tab__contracts__item"
            :style="{
              fontSize: isDesktop ? '1rem' : '1.1rem',
              borderLeft: isDesktop
                ? ''
                : getStatusColor(contract.status, true) + ' 2px solid',
              paddingLeft: isDesktop ? '' : '4px',
              gridTemplateColumns: '1fr 1fr 1fr 1fr .4fr',
            }"
            v-for="(contract, i) in computedContracts[activePage - 1]"
            :key="i"
          >
            <div class="tab__contracts__item-title" v-if="isDesktop">
              {{ contract.title }}
            </div>
            <div class="tab__contracts__item-pin">
              {{ contract.pin }}
            </div>
            <div class="tab__contracts__item-amount">
              {{ computedAmount(contract) }}
            </div>
            <div class="tab__contracts__item-status" v-if="isDesktop">
              <va-badge outline :color="getStatusColor(contract.status)">
                {{ contract.status }}
              </va-badge>
            </div>
            <va-button flat color="primary" @click="openContract(contract.pin)">
              <va-icon name="fa fa-external-link" />
            </va-button>
          </div>
        </div>
        <div class="tab__pagination_wrap" v-if="pages > 1">
          <va-pagination
            v-model="activePage"
            :pages="pages"
            visiblePages="5"
            small
          ></va-pagination>
        </div>
      </div>
    </div>
  </va-inner-loading>
</template>

<script>
import * as TradeGuard from '@/api/TradeGuard'

import _ from 'lodash'
import axios from 'axios'

export default {
  name: 'profile-trade-guard-tab',
  props: ['loading', 'user'],
  data: () => ({
    contracts: [],
    selectedSymbol: 'BTC',
    title: '',
    updating: false,
    activePage: 1,
    coinAmount: 0,
    tradePin: 0,
  }),
  computed: {
    computedStyle() {
      return {
        color: this.$themes.brightForeground,
        opacity: this.updating || this.loading ? 0.2 : 1,
      }
    },
    pages() {
      return _.chunk(this.contracts, 6).length
    },
    computedContracts() {
      let result = _.chunk(
        this.contracts.sort((ta, tb) =>
          ta.timestamp > tb.timestamp ? -1 : ta.timestamp < tb.timestamp ? 1 : 0
        ),
        6
      )

      return result
    },
    isDesktop() {
      return this.$store.getters.isDesktop
    },
  },
  methods: {
    create() {
      this.updating = true

      TradeGuard.createContract(
        this.title,
        this.tradePin,
        this.coinAmount,
        this.selectedSymbol
      )
        .then(response => {
          this.updating = false

          this.showToast(response.data.message, {
            icon: 'fa-check',
            position: 'bottom-center',
            duration: 4000,
            type: 'success',
          })
          this.updateContracts()
        })
        .catch(err => {
          this.updating = false

          if (err.response) {
            this.showToast(err.response.data.message, {
              icon: 'fa-times',
              position: 'bottom-center',
              duration: 4000,
              type: 'primary',
            })
          } else if (err.request) {
            console.log(err.request)
          } else {
            console.log(err)
          }
        })

      this.randomizeTradePin()
    },
    filterTradePin() {
      this.tradePin = this.tradePin.replace(/[^0-9]/, '').substr(0, 4)
    },
    randomizeTradePin() {
      this.tradePin = Math.floor(Math.random() * 9999).toString()
      this.tradePin = '0'.repeat(4 - this.tradePin.length) + this.tradePin
    },
    openContract(pin) {
      const base = this.$store.getters.BASE + ''
      window.open(base + '/trade-guard/contract?pin=' + pin, '_blank')
    },
    updateContracts() {
      this.updating = true
      TradeGuard.getContracts()
        .then(response => {
          this.contracts = response.data
          this.updating = false
        })
        .catch(err => {
          this.updating = false
          console.log(err)
        })
    },
    computedAmount(contract) {
      return contract.amount + ' ' + contract.symbol
    },
    getStatusColor(status, css) {
      return {
        'waiting for agreement': css ? '#EAD94C' : 'warning',
        completed: css ? '#00ff00' : 'success',
      }[status]
    },
  },
  mounted() {
    this.updateContracts()
    this.randomizeTradePin()
  },
}
</script>

<style lang="scss" scoped>
.tab {
  &__create {
    justify-items: center;
  }

  &__contracts {
    display: flex;
    flex-direction: column;
    width: calc(100% + 40px);
    padding: 0 20px;
    margin: 0 -20px;

    &__item {
      display: grid;
      width: 100%;

      div {
        display: grid;
        align-self: center;
        justify-self: center;
      }
    }

    &__item:nth-child(odd) {
      background-color: #4d4d4d11;
    }
  }

  &__pagination_wrap {
    display: grid;
    justify-content: center;
    height: 2rem;
  }
}
</style>
