<template>
  <va-card
    :color="$themes.background"
    :style="computedWidgetStyle"
    :title="$t('dashboard.total-log.title')"
  >
    <va-inner-loading :loading="!!(computedActions.length && loading)">
      <div v-if="computedActions.length" class="tab" :style="computedStyle">
        <div class="row">
          <div class="flex xs12 xl12">
            <div class="tab__wrap">
              <div class="tab__wrap__title">
                <div>User</div>
                <div>Action</div>
                <div>Date</div>
              </div>
              <div class="tab__wrap__table">
                <div
                  v-for="action in computedActions"
                  :key="action._id"
                  class="tab__wrap__table__item align--center"
                >
                  <div class="tab__wrap__table__item__title">
                    <div :style="itemStyle">User</div>
                    <div :style="itemStyle">Action</div>
                    <div :style="itemStyle">Date</div>
                  </div>
                  <div class="tab__wrap__table__item__props">
                    <div
                      :style="{
                        cursor: action.userId ? 'pointer' : '',
                        background: !action.userId ? 'none' : '',
                        ...itemStyle,
                      }"
                      @click="openProfile(action.userId)"
                      class="tab__wrap__table__item__props-name"
                    >
                      <va-icon
                        v-if="action.userId"
                        class="tab__wrap__table__item__props-name-icon"
                        name="fa fa-external-link"
                      />
                      {{ formatUsername(action.username) }}
                    </div>
                    <div
                      :style="itemStyle"
                      class="tab__wrap__table__item__props-action"
                    >
                      <va-icon
                        :name="actionIcon(action.actionName)"
                        class="ml-3 mr-3"
                      ></va-icon>
                      {{ $t(action.messageLocalPath) }}
                      {{ action.relatedData }}
                    </div>
                    <div
                      :style="itemStyle"
                      class="tab__wrap__table__item__props-date"
                      style="text-align: center"
                    >
                      {{ action.formatedDate }}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="tab__pagination_wrap">
          <va-pagination
            v-model="activePage"
            :pages="pages"
            :visiblePages="5"
            small
          ></va-pagination>
        </div>
      </div>
      <VaCardNoData
        v-else
        :loading="loading"
        :loading-text="$t('dashboard.no-data.log.loading')"
        :empty-text="$t('dashboard.no-data.log.empty')"
      />
    </va-inner-loading>
  </va-card>
</template>

<script>
import VaCardNoData from './components/VaCardNoData'
import _ from 'lodash'
import * as Users from '@/api/Users'

export default {
  name: 'dashboard-total-log',
  components: {
    VaCardNoData,
  },
  data: () => ({
    loading: false,
    activePage: 1,
    actions: [],
    pages: 1,
    startedSilentRefreshing: false,
    timeouts: {
      silentRefresh: null,
    },
  }),
  computed: {
    itemStyle() {
      return {
        color: this.$themes.brightForeground,
        border: `1px solid ${this.$themes.brightForeground}11`,
      }
    },
    computedStyle() {
      return {
        color: this.$themes.brightForeground,
        opacity: this.loading ? 0.2 : 1,
      }
    },
    computedWidgetStyle() {
      return {
        color: this.$themes.brightForeground,
      }
    },
    computedActions() {
      return this.actions[this.activePage - 1] || []
    },
  },
  methods: {
    formatUsername(name) {
      let sub = name.substring(0, 18)

      if (sub != name) sub += '...'

      return sub
    },
    openProfile(id) {
      if (!id) return

      this.loading = true
      this.$root.$emit('get-user', { id })
    },
    loaded() {
      this.loading = false
    },
    silentRefresh() {
      this.startedSilentRefreshing = true

      Users.getLogs()
        .then(({ data }) => {
          let self = this

          data.logs = data.logs.map(action => ({
            ...action,
            formatedDate: action.fromNow || self.$time.format(action.at),
          }))

          this.actions = _.chunk(data.logs, 8)
          this.pages = this.actions.length

          this.timeouts.silentRefresh = setTimeout(this.silentRefresh, 1000)
        })
        .catch(() => {
          this.timeouts.silentRefresh = setTimeout(this.silentRefresh, 20000)
        })
    },
    refresh() {
      this.loading = true

      Users.getLogs()
        .then(({ data }) => {
          let self = this

          data.logs = data.logs.map(action => ({
            ...action,
            formatedDate: action.fromNow || self.$time.format(action.at),
          }))

          this.actions = _.chunk(data.logs, 8)
          this.pages = this.actions.length

          this.loaded()

          if (!this.startedSilentRefreshing) {
            this.silentRefresh()
          }
        })
        .catch(() => {
          this.loaded()
        })
    },
    actionIcon(name) {
      return {
        registered: 'fa fa-user-plus',
        authenticated: 'fa fa-sign-in',
        visited: 'fa fa-map-marker',
        name_changed: 'fa fa-edit',
        password_changed: 'fa fa-edit',
        transfer: 'fa fa-money',
        staking: 'fa fa-money',
        withdrawal: 'fa fa-money',
        deposit: 'fa fa-money',
        'applied-promo': 'fa fa-ticket',
      }[name]
    },
  },
  mounted() {
    this.refresh()
  },
  created() {
    this.$root.$on('open-profile', this.loaded)
  },
  beforeDestroy() {
    this.$root.$off('open-profile', this.loaded)

    Object.values(this.timeouts).forEach(t => {
      clearTimeout(t)
    })
  },
}
</script>

<style lang="scss" scoped>
@import '../../sass/smart-grid.scss';

.tab {
  min-height: 232px;

  &__wrap {
    &__title {
      font-weight: 600;
      width: calc(100% + 40px);
      margin: 0 -20px;
      height: 100%;

      @include to(80rem) {
        display: none;
      }

      @include from(80rem) {
        height: 40px;
        display: grid;
        justify-items: center;
        align-items: center;
        grid-template-columns: 16rem 1fr 150px;
      }
    }

    &__table {
      &__item {
        width: calc(100% + 40px);
        margin: 0 -20px;
        background-color: #00000010;

        @include to(80rem) {
          display: grid;
          grid-template-columns: 70px 1fr;
        }

        @include from(80rem) {
          display: block;
        }

        &__title {
          height: 100%;

          div {
            display: grid;
            align-content: center;
            text-indent: 10px;
            width: 100%;
            height: 100%;

            border: 1px #ffffff11 solid;
          }

          @include to(80rem) {
            display: grid;
            grid-template-rows: 2fr 1fr 1fr;
            justify-items: center;
            align-items: center;
          }

          @include from(80rem) {
            display: none;
          }
        }

        &__props {
          min-height: 55px;

          @include to(80rem) {
            display: grid;
            grid-template-rows: 2fr 1fr 1fr;
          }

          @include from(80rem) {
            display: grid;
            grid-template-columns: 16rem 1fr 150px;
          }

          div {
            width: 100%;
            height: 100%;
            display: flex;
            border: 1px #ffffff11 solid;
            overflow: hidden;
            padding: 10px 2rem;
          }

          &-action {
            align-items: center;
          }

          &-name {
            width: 100%;
            text-align: left;
            position: relative;
            transition: padding 0.5s;

            &-icon {
              top: 0;
              right: 0;
              opacity: 0.4;
              margin-right: 0.4rem;
            }

            &:hover {
              padding: 0 3rem;
              background-color: #ffffff07;

              &-icon {
                opacity: 1;
              }
            }
          }

          &-date,
          &-name {
            align-items: center;
            justify-content: flex-start;
          }

          &-name {
            padding: 0 1rem;
          }
        }
        &:nth-child(odd) {
          background-color: #ffffff10;
        }
      }
    }
  }

  &__pagination_wrap {
    display: grid;
    justify-content: center;
    height: 2rem;
  }
}
</style>
