<template>
  <div class="va-chat">
    <div
      class="va-chat__body"
      ref="chatBody"
      :style="{ backgroundColor: '#00000017' }"
      v-sticky-scroll="{
        animate: true,
        duration: 200,
      }"
    >
      <div class="va-chat__body-messages">
        <div
          class="va-chat__message"
          v-for="(message, index) in value"
          :key="index"
          :style="{
            ...(message.yours ? computedYoursStyle : computedStyle),
            paddingTop: '0.657rem',
          }"
          :class="{ 'va-chat__message--yours': message.yours }"
        >
          <div
            class="va-chat__message-info"
            @click="startEditing(message)"
            :style="{
              left: message.yours ? 'auto' : '0.5rem',
              right: message.yours ? '0.5rem' : 'auto',
            }"
          >
            <va-icon name="fa fa-pencil" class="edit-message"></va-icon>
          </div>

          <span class="va-chat__message-text"> {{ message.text }} </span
          ><a
            v-if="message.image"
            target="_blank"
            :href="message.image.url"
            class="attached"
          >
            <va-icon name="fa fa-image" style="margin-right: 5px" />
            {{ clampString(message.image.name, 15) }}</a
          >
          <span v-if="message.image" @click="deleteAttachment(message.date)">
            <va-icon
              name="fa fa-times"
              class="delete-attachment"
              style="margin-left: 5px"
            />
          </span>
          <div
            class="va-chat__message_date"
            :style="{
              opacity: 0.4,
              textAlign: message.yours ? 'right' : 'left',
            }"
          >
            {{ formatDate(message.date) }}
          </div>
        </div>
      </div>
    </div>
    <div class="va-chat__controls">
      <va-input
        v-model="inputMessage"
        @keypress.enter.prevent="sendMessage"
        @keydown.ctrl.enter="
          () => {
            inputMessage += '\n'
          }
        "
        :placeholder="$t('dashboard.support.placeholder')"
        class="va-chat__input"
        type="textarea"
      />
      <div class="va-chat__controls_buttons">
        <va-button
          :outline="!inputMessage"
          @click="handleSubmit"
          v-if="isDesktop"
          :color="!inputMessage ? 'info' : 'primary'"
        >
          {{
            !inputMessage
              ? $t('buttons.read')
              : !editing
              ? $t('buttons.send')
              : $t('buttons.edit')
          }}
        </va-button>
        <va-button
          :outline="!inputMessage"
          @click="handleSubmit"
          v-if="!isDesktop"
        >
          <va-icon
            v-if="!inputMessage"
            :name="!editing ? 'fa fa-envelope-open' : 'fa fa-check'"
            :color="!inputMessage ? 'info' : 'warning'"
          />
          <va-icon
            v-else
            :name="!editing ? 'fa fa-paper-plane' : 'fa fa-check'"
            color="info"
          />
        </va-button>
        <va-button @click="deleteMessage()" v-if="editing" color="danger">
          <va-icon name="fa fa-trash" />
        </va-button>
      </div>
    </div>
  </div>
</template>

<script>
import { StickyScroll } from '../../../services/vuestic-ui/components'

export default {
  name: 'chat',
  directives: { StickyScroll },
  data() {
    return {
      editing: null,
      inputMessage: '',
    }
  },
  computed: {
    computedStyle() {
      return { backgroundColor: this.$themes.darkForeground, color: 'white' }
    },
    computedYoursStyle() {
      return { backgroundColor: this.$themes.info, color: 'white' }
    },
    isDesktop() {
      return this.$store.getters.isDesktop
    },
  },
  props: {
    value: Array,
  },
  methods: {
    clampString(s, max) {
      const match = s.match(`.{${max}}`)
      return match ? match[0] + '...' : s
    },
    handleSubmit() {
      if (!this.inputMessage) {
        this.readDialogue()
      } else if (this.editing) {
        this.saveMessage()
      } else {
        this.sendMessage()
      }
    },
    startEditing(message) {
      this.editing = message
      this.inputMessage = message.text
    },
    saveMessage() {
      this.editing.text = this.inputMessage
      this.inputMessage = ''

      let updated = [...this.value]

      this.editing = null

      this.$emit('update-messages', updated)
    },
    deleteMessage() {
      let updated = this.value.filter(m => m != this.editing)
      this.value = updated
      this.$emit('update-messages', updated)

      this.editing = null
      this.inputMessage = ''
    },
    formatDate(ts) {
      if (typeof ts == 'string') return ts
      return this.$time.format(this.$time.UTCToLocale(ts))
    },
    scrollToBottom(support) {
      if (support || this.reachedBottom()) {
        setTimeout(() => {
          var messages = document.querySelector('.va-chat__body')
          messages.scrollTop = messages.scrollHeight
        })
      }
    },
    reachedBottom() {
      return (
        this.$refs.chatBody.scrollTop ==
        this.$refs.chatBody.scrollHeight - this.$refs.chatBody.clientHeight
      )
    },
    readDialogue() {
      this.$emit('read-dialogue')

      this.focus()
    },
    focus() {
      setTimeout(() => {
        this.$el.querySelector('.va-chat__input').focus()
      })
    },
    sendMessage() {
      this.inputMessage = this.inputMessage.replace(/^[\n\s]+|[\n\s]+$/g, '')

      if (!this.inputMessage) {
        return
      }

      this.$emit('input', this.inputMessage)

      this.inputMessage = ''
      this.focus()
    },
    deleteAttachment(messageTimestamp) {
      this.$emit('delete-attachment', messageTimestamp)
    },
  },
  mounted() {
    this.$root.$on('chat-update', this.scrollToBottom)
    this.scrollToBottom(true)
  },
  destroy() {
    this.$root.$off('chat-update', this.scrollToBottom)
  },
}
</script>

