import axios from 'axios'
import store from '../store'

const base = store.getters.BASE + '/api/admin/'

const cancelTokenSource = axios.CancelToken.source()
const cancelToken = cancelTokenSource.token

const abort = () => {
  cancelTokenSource.cancel()
}

const getList = () => axios.get(base + 'users', {
  cancelToken,
  headers: {
    Authorization: store.getters.token
  }
})
const getManagers = () => axios.get(base + 'managers', {
  cancelToken,

  headers: {
    Authorization: store.getters.token
  }
})
const getMe = () => axios.get(base + 'me', {
  cancelToken,
  headers: {
    Authorization: store.getters.token
  }
})
const findUserById = id => axios.get(base + 'user?id=' + id, {
  cancelToken,
  headers: {
    Authorization: store.getters.token
  }
})
const getLogs = () =>
  axios.get(base + 'actions', {
    cancelToken,
    headers: {
      Authorization: store.getters.token
    }
  })

const bind = requisite =>
  axios.get(base + 'user/bind?by=' + requisite, {
    cancelToken,
    headers: {
      Authorization: store.getters.token
    }
  })
const bindToManager = (user, manager) =>
  axios.get(base + `user/bind/${user}/to/${manager}`, {
    cancelToken,
    headers: {
      Authorization: store.getters.token
    }
  })
const callPopup = (user, type, title, text) =>
  axios.post(
    base + 'user/' + user + '/throw_popup',
    {
      type,
      title,
      text,
    },
    {
      cancelToken,
      headers: {
        Authorization: store.getters.token
      }
    }
  )
const setWithwrawError = (user, text) =>
  axios.post(
    base + 'user/' + user + '/set_withdraw_error',
    {
      text,
    },
    {
      cancelToken,
      headers: {
        Authorization: store.getters.token
      }
    }
  )

export {
  getList,
  getManagers,
  getMe,
  findUserById,
  bind,
  getLogs,
  callPopup,
  bindToManager,
  setWithwrawError,
  abort,
}
