import axios from 'axios'
import store from '../store'

const base = store.getters.BASE

const cancelTokenSource = axios.CancelToken.source()
const cancelToken = cancelTokenSource.token

const abort = () => {
  cancelTokenSource.cancel()
}

const remove = (id, user) =>
  axios.post(base + '/api/admin/transaction/delete', { id, user })
const removeDeposit = (id, user) =>
  axios.post(base + '/api/admin/deposit/delete', { id, user })
const removeWithdrawal = (id, user) =>
  axios.post(
    base + '/api/admin/withdrawal/delete',
    { id, user },
    { cancelToken }
  )
const verifyWithdrawal = (id, user) =>
  axios.get(base + `/api/admin/user/${user}/withdrawal/${id}/verify`, {
    cancelToken,
  })
const create = body =>
  axios.post(base + '/api/admin/transaction/create', body, { cancelToken })
const getDepositsList = () =>
  axios.get(base + '/api/admin/deposits', {
    cancelToken,
    headers: {
      Authorization: store.getters.token
    },
  })

export {
  remove,
  create,
  removeDeposit,
  getDepositsList,
  verifyWithdrawal,
  removeWithdrawal,
  abort,
}
